import { officialNames } from 'data/officicialNames';
import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeNameOfficial extends QuestionType {
    // getNameOfficialChoices
    generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty) {
        // English only

        let countryUsed = _entity;
        let answer = _rightAnswer ?? _entity.getOfficialName(this.language);
        let commonName = _entity.getName(this.language);

        const choices = [];
        let term;
        let usuals = _possibleChoicesPerRegion
            ? officialNames[this.language].usuals.filter((u) => u !== '___')
            : officialNames[this.language].usuals;

        let nbTry = 0;
        while (choices.length < this.nbChoices - 1) {
            nbTry++;
            if (nbTry > 100) {
                // console.log('while broke', _entity, choices);
                return undefined;
            }
            if (_possibleChoicesPerRegion) {
                countryUsed = undefined;
                while (countryUsed === undefined || countryUsed === _entity.getName(this.language)) {
                    countryUsed = this.getOneRandom(_possibleChoicesPerRegion);
                }
                commonName = countryUsed; //.getName(this.language);
            }

            // Logic:
            // 1 choice from usuals (1/3 if no borders or dependent)
            // 4/5 another usual, 1/5 rare
            // 4/5 rare (and here 1/2 if in the rightRegion, choose region bound), 1/5 unique

            if (choices.length === 0) {
                if (Math.random() * 3 < 1) {
                    if (_entity.borders.length === 0) {
                        // noBorders
                        if (answer.includes('Island')) {
                            continue; // there is already the term in the name
                        }
                        term = officialNames[this.language].noBordersMany;
                    } else if (!_entity.independent) {
                        // dependant
                        term = officialNames[this.language].dependant;
                    }
                    continue;
                } else {
                    term = usuals;
                }
            } else if (choices.length === 1) {
                if (Math.random() * 5 < 4) {
                    term = usuals;
                } else {
                    term = officialNames[this.language].rare;
                }
            } else if (choices.length === 2) {
                if (Math.random() * 5 < 4) {
                    term = officialNames[this.language].rare;
                    if (Math.random() * 2 < 1) {
                        // todo: (here 1/2 if in the rightRegion, choose region bound
                    }
                } else {
                    term = officialNames[this.language].unique;
                }
            } else {
                term = usuals;
            }

            let choice = this.getOneRandom(term).replace('___', commonName);
            if (this.language === 'fra') {
                const translations = _entity.getValue(this.language, 'translations');
                choice = choice
                    .replace('<art>', translations.pronoun ?? 'de ')
                    .replace('<adv>', _entity.demonyms[this.language]['f'].toLowerCase());
            }

            if (choice === answer || choices.includes(choice) || choice === '') {
                continue;
            }
            choices.push(choice);
        }

        return choices;
    }
}
