import { ENTITY_SEA } from 'config/baseConfig';

import { getCountries } from 'data/data';

export class SeaLake {
    constructor(obj, isSea = true) {
        this.isWater = true;
        this.isSea = isSea;
        this.entityType = ENTITY_SEA;

        this.key = obj.key;
        this.type = obj.type.split(',');
        this.min_label = obj.min_label;
        this.max_label = obj.max_label;
        this.scalerank = obj.scalerank;
        this.borders = obj.borders?.split(',')?.map((c) => getCountries()[c.toUpperCase()]);
        this.area = obj.area;
        this.depth = obj.depth;
        this.volume = obj.volume;

        // Translatable
        this.name = obj.name;
    }
    getRegion(lang) {
        return this.borders
            .map((c) => (c.subregion ? c.subregion : c.region)[lang])
            .filter((value, index, array) => array.indexOf(value) === index)
            .join(', ');
    }
    get regionCode() {
        // console.log(this.borders);
        return this.borders?.[0].regionCode;
    }
    get subregionCode() {
        return this.borders?.[0].subregionCode;
    }

    getName(lang) {
        return this.name[lang] || this.name.eng;
    }

    getValue(lang, valueName, nbFromList) {
        let value;
        switch (valueName) {
            case 'name':
                value = this.getName(lang);
                break;
            case 'country':
                value = this.borders.map((c) => c.getName(lang));
                // value = nbFromList === 1 ? this.getOneRandom(country) : country;
                break;
            default:
                value = this[valueName];
                break;
        }
        return value;
    }
}
