import { ENTITY_CITY, ENTITY_COUNTRY, ENTITY_LANDMARK, ENTITY_PEAK, ENTITY_SEA } from 'config/baseConfig';

import { GameMode } from 'store/quizBuilder/gameModes/GameMode';

export class GameModeTrivia extends GameMode {
    constructor(_gameConfig, _mode, _region, _difficultyLevel, _independantOnly, _language) {
        super(_gameConfig, _mode, _region, _difficultyLevel, _independantOnly, _language);
        this.choiceUseFilteredEntities = false;
    }

    generateEntities() {
        // générer les entités en fonction de la config et des questionTypes disponibles
        const entityModes = this.mode.questionType
            ?.map((qt) => qt.entityType)
            .filter((val, index, arr) => arr.indexOf(val) === index);

        Object.keys(entityModes).forEach((entityKey) => {
            const entityMode = entityModes[entityKey];
            if (entityMode) {
                this.entities[entityMode] = this.generateEntitiesForType(entityMode);
            }
        });
    }

    filterEntities() {
        this.entities.filteredEntities = [
            ...(this.entities[ENTITY_COUNTRY] ? this.entities[ENTITY_COUNTRY] : []), // Will always be there...
            ...(this.entities[ENTITY_CITY]
                ? this.entities[ENTITY_CITY].splice(0, this.entities[ENTITY_COUNTRY].length * 0.2)
                : []),
            ...(this.entities[ENTITY_PEAK]
                ? this.entities[ENTITY_PEAK].splice(0, this.entities[ENTITY_COUNTRY].length * 0.2)
                : []),
            ...(this.entities[ENTITY_LANDMARK]
                ? this.entities[ENTITY_LANDMARK].splice(0, this.entities[ENTITY_COUNTRY].length * 0.2)
                : []),
            ...(this.entities[ENTITY_SEA]
                ? this.entities[ENTITY_SEA].splice(0, this.entities[ENTITY_COUNTRY].length * 0.2)
                : []),
        ];
    }

    generateQuestions(_gameLength) {
        const nbQuestions = this.getNbQuestions(_gameLength);

        let questionTypes = this.shuffle(this.duplicate(this.mode.questionType));
        // console.log(questionTypes);
        const questions = [];
        let rndEntity;
        let questionType;
        let question;
        let nbTryQ = 0;
        let nbTry = 0;
        while (questions.length < nbQuestions && nbTryQ < 10) {
            if (questionTypes.length === 0) {
                questionTypes = this.shuffle(this.duplicate(this.mode.questionType));
            }
            question = undefined;
            // questionType = this.mode.questionType.find((qt) => qt.key === 'country_largest'); // the the most something SEA AND PEAK
            questionType = questionTypes.pop();
            // console.log(questionType.key, questionType.entityType);
            nbTry = 0;
            while (!question) {
                rndEntity = this.getOneRandom(this.getEntities(questionType.entityType));
                question = this.getQuestion(rndEntity, questionType.key);
                // console.log(nbTry, rndEntity, question);
                nbTry++;
                if (nbTry > 10) {
                    break;
                }
            }
            if (question) {
                questions.push(question);
            } else {
                // console.log(nbTry);
                nbTryQ++;
            }
        }

        // console.log('generateQuestions', nbQuestions, questions);
        return questions;

        // this.entities.filteredEntities
    }

    duplicate(list) {
        return [...list];
    }
}
