import { useTheme } from '@emotion/react';
import { ROUTE_TEST } from 'config/routes';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router';

import ArrowCircleLeftIcon from '@mui/icons-material/ArrowCircleLeft';
import AutorenewIcon from '@mui/icons-material/Autorenew';
import CancelIcon from '@mui/icons-material/Cancel';
import SettingsIcon from '@mui/icons-material/Settings';
import { Box, IconButton, Typography, alpha } from '@mui/material';

import Settings from 'components/layout/Settings';
import ConfirmDialog from 'components/ui/ConfirmDialog';
import FunTypo from 'components/ui/FunTypo';
import { newGame, setPage } from 'store/actions';
import { useCurrentGame, useGameCategory, useGameConfig, useIsInit, usePage } from 'store/selector';
import { getGameSVG, pxToRem, pxToRemInt } from 'util/util';

export const TOP_NAV_HEIGHT = pxToRemInt(56);
export const TOP_NAV_HEIGHT_REM = pxToRem(56);

const Header = (props) => {
    const { deferredPrompt } = props;
    const dispatch = useDispatch();
    const isInit = useIsInit();
    const theme = useTheme();
    const { t, i18n } = useTranslation();
    const navigate = useNavigate();
    const gameConfig = useGameConfig();
    const game = useCurrentGame();
    const page = usePage();
    const gameCategory = useGameCategory();

    const [confirmCancelGameOpen, setConfirmCancelGameOpen] = useState(false);

    // const mdgUp = useMediaQuery((theme) => theme.breakpoints.up('md'));

    const handleClickCloseGame = (e) => {
        setConfirmCancelGameOpen(true);
    };

    // Settings Delete Dialog
    const [settingsOpen, setSettingsOpen] = useState(false);
    const handleClickSettings = (e) => {
        setSettingsOpen(true);
    };
    const handleSettingsClose = () => {
        setSettingsOpen(false);
    };
    // Confirm Quit Game Dialog
    const handleConfirmCancelGameClose = () => {
        setConfirmCancelGameOpen(false);
        dispatch(newGame());
    };
    const handleConfirmCancelGameDisagree = () => {
        setConfirmCancelGameOpen(false);
    };
    // const closeRef = React.useRef < HTMLElement > null;
    // const handleEntering = () => {
    //     if (closeRef.current != null) {
    //         closeRef.current.focus();
    //     }
    // };
    const handleClickHome = () => {
        dispatch(setPage('menu'));
    };
    const handleClickSwapRoute = () => {
        navigate(ROUTE_TEST);
    };
    const isEnd = game && game.currentTurn >= game.questions.length;
    const inGame = page === 'game';
    const inWiki = page === 'wiki';

    const isDev = !process.env.NODE_ENV || process.env.NODE_ENV === 'development';
    const showTest = false && !inWiki && isDev;

    return (
        <>
            <Box
                component="header"
                display="flex"
                flex="0 1 auto"
                flexDirection="row"
                justifyContent={!inWiki && !inGame ? 'flex-end' : 'space-between'}
                alignItems="center"
                sx={{
                    height: TOP_NAV_HEIGHT_REM,
                    position: 'relative',
                }}
            >
                {inWiki && (
                    <>
                        <IconButton
                            aria-label={t('Home')}
                            onClick={handleClickHome}
                            color="text"
                            padding="1px"
                            className="standalone"
                        >
                            <ArrowCircleLeftIcon />
                        </IconButton>
                        <Typography
                            component="h1"
                            onClick={handleClickHome}
                            sx={{
                                height: TOP_NAV_HEIGHT_REM,
                                lineHeight: TOP_NAV_HEIGHT_REM,
                                textAlign: 'center',
                                cursor: 'pointer',
                            }}
                        >
                            <FunTypo
                                text={t(gameConfig?.name)}
                                color={theme.palette.text.title.replace('#', '')}
                                stroke={true}
                                strokeWidth={pxToRem(1)}
                                distance={pxToRem(2)}
                                sx={{
                                    fontSize: pxToRem(36),
                                    lineHeight: TOP_NAV_HEIGHT_REM,
                                    fontWeight: 800,
                                    textAlign: 'center',
                                }}
                            />
                            &nbsp;
                            <FunTypo
                                text={t('Quiz')}
                                color={theme.palette.text.title.replace('#', '')}
                                stroke={false}
                                strokeWidth={pxToRem(1)}
                                distance={pxToRem(2)}
                                sx={{
                                    fontSize: pxToRem(36),
                                    lineHeight: TOP_NAV_HEIGHT_REM,
                                    fontWeight: 800,
                                    textAlign: 'center',
                                }}
                            />
                        </Typography>
                    </>
                )}
                {showTest && (
                    <IconButton className="standalone" onClick={handleClickSwapRoute} color="text" padding="1px">
                        <AutorenewIcon />
                    </IconButton>
                )}
                {inGame && (
                    <Typography
                        component="h1"
                        display="inline"
                        fontSize={pxToRem(20)}
                        color="secondary.alpha50"
                        fontWeight={800}
                        sx={{
                            display: 'flex',
                            alignItems: 'center',
                            svg: {
                                color: theme.palette.secondary.contrast,
                                height: pxToRem(32),
                                width: pxToRem(32),
                                marginLeft: pxToRem(-4),
                                marginRight: pxToRem(4),
                                fill: 'currentColor',
                                filter: `drop-shadow(2px 2px 0px ${alpha(theme.palette.shadow, 0.15)})`,
                            },
                        }}
                    >
                        {gameCategory && (
                            <>
                                {getGameSVG(gameCategory.key)}
                                <Typography
                                    component="span"
                                    color="secondary.contrast"
                                    fontSize={pxToRem(18)}
                                    fontWeight={800}
                                >
                                    {gameCategory.name[i18n.language]}
                                </Typography>
                            </>
                        )}
                        {/*&nbsp;&#183;&nbsp;
                        <Typography component="span" color="secondary.alpha50" fontSize={pxToRem(18)} fontWeight={600}>
                            {region ? region[i18n.language] : t('World')}
                        </Typography>
                        &nbsp;&#183;&nbsp;
                        <Typography component="span" color=".alpha50" fontSize={pxToRem(18)} fontWeight={600}>
                            {t(configGo.difficultyLevels.find((d) => d.key === game.difficultyLevel).name)}
						</Typography>*/}
                    </Typography>
                )}
                {inGame && (
                    <>
                        {!isEnd && (
                            <IconButton
                                className="standalone"
                                aria-label={t('Close')}
                                onClick={handleClickCloseGame}
                                color="text"
                                padding="1px"
                            >
                                <CancelIcon />
                            </IconButton>
                        )}
                    </>
                )}
                {((isInit && !inGame) || isEnd) && (
                    <>
                        <IconButton
                            className="standalone"
                            aria-label={t('Settings')}
                            onClick={handleClickSettings}
                            color="text"
                            padding="1px"
                            sx={{
                                margin: 'auto',
                            }}
                        >
                            <SettingsIcon />
                        </IconButton>
                    </>
                )}
            </Box>
            <ConfirmDialog
                open={confirmCancelGameOpen}
                title={t('Quit the game?')}
                leftHandler={handleConfirmCancelGameClose}
                leftLabel={t('Quit')}
                rightHandler={handleConfirmCancelGameDisagree}
                rightLabel={t('Continue')}
            />
            <Settings
                settingsDialogOpen={settingsOpen}
                handleClose={handleSettingsClose}
                deferredPrompt={deferredPrompt}
            />
        </>
    );
};

export default Header;
