import { t } from 'i18next';

import { shuffle } from 'util/util';

export class QuestionType {
    constructor(_gameMode, _questionType) {
        this.gameMode = _gameMode;
        this.questionType = _questionType;
        this.isChoiceFlat = this.getIsChoiceFlat(_questionType);
        // console.log('this.isChoiceFlat=' + this.isChoiceFlat, _questionType);
        this.choiceListKey = this.getChoiceListKey(_questionType);
    }

    getChoiceListKey(_questionType) {
        let key = '';
        key += _questionType.choiceEntityType ? (key ? '_' : '') + _questionType.choiceEntityType : '';
        // key += _questionType.choiceProperty ? (key ? '_' : '') + _questionType.choiceProperty : '';
        key += _questionType.choiceFilterProp ? (key ? '_' : '') + _questionType.choiceFilterProp : '';
        key += _questionType.choiceFilterProp
            ? (key ? '_' : '') + (_questionType.choiceFilterVal ? 'true' : 'false')
            : '';
        key += this.isChoiceFlat ? (key ? '_' : '') + 'flat' : '';
        // todo: faire une liste de chaque, incluant les choiceFilterProp et choiceFilterVal
        return key;
    }

    getIsChoiceFlat(_questionType) {
        // console.log(this.isExpert && !!_questionType.hasExpertAnswerHidden);
        const choicesByRegion = this.isHard || (this.isExpert && !_questionType.hasExpertAnswerHidden);
        return _questionType.choicesFlat || !choicesByRegion;
    }

    getChoicesList() {
        return this.gameMode.getChoicesList(this.questionType, this.choiceListKey, this.isChoiceFlat);
    }

    getSpecialChoicesList(_key, _generateChoiceListFunction) {
        let list = this.gameMode.getSpecialChoicesList(_key);
        if (!list) {
            list = _generateChoiceListFunction();
            this.gameMode.setSpecialChoicesList(_key, list);
        }
        return list;
    }

    getQuestion(_entity) {
        const questionText = this.getQuestionValue(_entity);
        let allAnswers = this.getAnswer(_entity);
        const choices = this.getChoices(_entity, allAnswers, this.getChoicesList());
        let answer;
        if (Array.isArray(allAnswers)) {
            answer = allAnswers.pop();
        } else {
            answer = allAnswers;
        }
        const answerAdditionnalText = this.getAdditionnalText(_entity, allAnswers);
        const questionPhraseValues = this.getQuestionPhraseValues(_entity);
        return { questionText, answer, choices, answerAdditionnalText, questionPhraseValues };
    }

    getQuestionValue(_entity) {
        if (this.questionType.questionProperty) {
            let questionValue = _entity.getValue(this.language, this.questionType.questionProperty, 1);
            if (Array.isArray(questionValue)) {
                return questionValue[Math.floor(Math.random() * questionValue.length)];
            }
            return questionValue;
        }
        return undefined;
    }

    getAnswer(_entity) {
        if (this.questionType.answerProperty) {
            let answer = _entity.getValue(this.language, this.questionType.answerProperty, 1);
            if (Array.isArray(answer)) {
                return this.shuffle(answer);
            }
            return answer;
        }
        return undefined;
    }

    getAdditionnalText(_entity, _allAnswers) {
        return _allAnswers && Array.isArray(_allAnswers) && _allAnswers.length > 0
            ? `${t('and')} ` + _allAnswers.join(', ')
            : undefined;
    }

    getQuestionPhraseValues(_entity) {
        return undefined;
    }

    getChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty = false) {
        if (!_rightAnswer || _rightAnswer.length === 0 || this.isFlashcard) {
            return undefined;
        }

        if (this.questionType.hardUseSameType && (this.isHard || this.isExpert)) {
            // choix basés sur le type
            const allEntitiesOfType = this.gameMode.getEntities(_entity.entityType);
            const entityType = _entity.type?.[0];
            let entityChoicesSameType = allEntitiesOfType.filter((l) => l?.type?.includes(entityType));
            const entityChoicesSameTypeAndReg = entityChoicesSameType.filter(
                (l) => l?.regionCode === _entity.regionCode
            );
            // console.log(entityChoicesSameTypeAndReg.length, entityChoicesSameType.length, entityType);
            if (entityChoicesSameType.length <= this.nbChoices) {
                entityChoicesSameType = allEntitiesOfType;
            } else if (entityChoicesSameTypeAndReg.length <= this.nbChoices) {
                entityChoicesSameType = entityChoicesSameTypeAndReg;
            }
            // console.log(_entity, _entity.regionCode);
            // console.log(_entity, _entity.regionCode, entityChoicesSameTypeAndReg);
            return this.generateChoices(
                _entity,
                _rightAnswer,
                entityChoicesSameType.length < this.nbChoices ? _possibleChoicesPerRegion : entityChoicesSameType,
                true
            );
        } else {
            return this.generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty);
        }
    }

    generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty = false) {
        // console.log(this.questionType.key, _possibleChoicesPerRegion);
        const choices = [];
        const expertChoices =
            this.isExpert && !_ignoreDifficulty && this.questionType.hasExpertAnswerHidden
                ? [this.getHardAnswer(_rightAnswer)]
                : [];
        const indexUsed = [];
        let answersFrom = _possibleChoicesPerRegion;

        if (!this.isChoiceFlat && !_ignoreDifficulty) {
            // console.log(this.questionType.key, _possibleChoicesPerRegion);
            let answersFromRegion = _possibleChoicesPerRegion[_entity.regionCode][_entity.subregionCode];
            if (answersFromRegion.length < 7) {
                let allFromRegion = [];
                Object.keys(_possibleChoicesPerRegion[_entity.regionCode]).forEach((reg) => {
                    if (reg !== _entity.subregionCode) {
                        allFromRegion = allFromRegion.concat(_possibleChoicesPerRegion[_entity.regionCode][reg]);
                    }
                }, []);
                if (_entity.regionCode === 'AN') {
                    Object.keys(_possibleChoicesPerRegion['OC']).forEach((reg) => {
                        allFromRegion = allFromRegion.concat(_possibleChoicesPerRegion['OC'][reg]);
                    }, []);
                }
                answersFrom = answersFromRegion.concat(
                    this.shuffle(allFromRegion).slice(0, 7 - answersFromRegion.length)
                );
            } else {
                answersFrom = answersFromRegion;
            }
            // console.log(answersFrom);
        }
        const max = answersFrom.length;

        let nbTry = 0;
        let tmpChoice;
        let tmpChoiceValue;
        while (choices.length < this.nbChoices - 1) {
            nbTry++;
            if (nbTry > 20) {
                // console.log('while broke', this.questionType.key, _entity, choices);
                return undefined;
            }
            const rnd = Math.floor(Math.random() * max);
            tmpChoice = answersFrom[rnd];
            tmpChoiceValue =
                typeof tmpChoice === 'string'
                    ? tmpChoice
                    : tmpChoice
                      ? tmpChoice.getValue(this.language, this.questionType.choiceProperty, 1)
                      : undefined;
            if (
                !this.canBeInChoices(
                    _ignoreDifficulty,
                    _rightAnswer,
                    choices,
                    indexUsed,
                    rnd,
                    _entity,
                    tmpChoice,
                    tmpChoiceValue,
                    expertChoices
                )
            ) {
                continue;
            }
            choices.push(tmpChoiceValue);
            indexUsed.push(rnd);
            if (this.isExpert && !_ignoreDifficulty) {
                expertChoices.push(this.getHardAnswer(tmpChoiceValue));
            }
            // aussi: choisir seulement parmis ceux de même région si on est en mode WORLD
        }

        return choices;
    }

    canBeInChoices(
        _ignoreDifficulty,
        _rightAnswer,
        choices,
        indexUsed,
        rnd,
        _entity,
        tmpChoice,
        tmpChoiceValue,
        expertChoices
    ) {
        // console.log(
        //     tmpChoice,
        //     indexUsed.includes(rnd),
        //     tmpChoiceValue === '',
        //     tmpChoiceValue === undefined,
        //     _rightAnswer.includes(tmpChoiceValue),
        //     choices.includes(tmpChoiceValue)
        // );
        return !(
            indexUsed.includes(rnd) ||
            tmpChoiceValue === '' ||
            tmpChoiceValue === undefined ||
            _rightAnswer.includes(tmpChoiceValue) ||
            choices.includes(tmpChoiceValue) ||
            (this.isExpert && !_ignoreDifficulty && expertChoices.includes(this.getHardAnswer(tmpChoiceValue)))
        );
    }

    getHardAnswer(_answer) {
        return _answer[0] + _answer[_answer.length - 1];
    }

    get nbChoices() {
        return this.gameMode.gameConfig.nbChoices;
    }

    get language() {
        return this.gameMode.language;
    }

    get isFlashcard() {
        return this.gameMode.isFlashcard;
    }

    get isNormal() {
        return this.gameMode.isNormal;
    }

    get isHard() {
        return this.gameMode.isHard;
    }

    get isExpert() {
        return this.gameMode.isExpert;
    }

    getOneRandom(_list) {
        return _list && _list.length ? _list[Math.floor(Math.random() * _list.length)] : undefined;
    }

    shuffle(_list) {
        return shuffle(_list);
    }
}
