import {
    createTheme,
    filledInputClasses,
    iconButtonClasses,
    inputLabelClasses,
    linearProgressClasses,
    outlinedInputClasses,
    paperClasses,
    switchClasses,
    tableCellClasses,
    toggleButtonClasses,
    toggleButtonGroupClasses,
} from '@mui/material';

import { pxToRem } from 'util/util';

// Used only to create transitions
const muiTheme = createTheme();

const borderWidth = 0;
const width = pxToRem(40);
const height = pxToRem(24);
const size = pxToRem(18);
const gap = (24 - 18) / 2;

export function createComponents(config) {
    const { palette } = config;

    return {
        MuiAutocomplete: {
            styleOverrides: {
                inputRoot: {
                    backgroundColor: palette.background.input,
                    '&:hover': {
                        backgroundColor: palette.background.input,
                    },
                },
                paper: {
                    boxShadow: '0px 5px 10px rgba(0, 0, 0, 0.2), 0px 0px 0px 0.5px rgba(0, 0, 0, 0.075)',
                    backgroundColor: palette.background.input,
                },
            },
        },
        MuiButton: {
            styleOverrides: {
                root: ({ ownerState }) => ({
                    borderRadius: pxToRem(12),
                    textTransform: 'none',
                    boxShadow: '0px 5px 10px rgba(0, 0, 0, 0.2), 0px 0px 0px 0.5px rgba(0, 0, 0, 0.075)',
                }),
                sizeSmall: {
                    padding: pxToRem(6) + ' ' + pxToRem(16),
                },
                sizeMedium: {
                    padding: pxToRem(8) + ' ' + pxToRem(20),
                },
                sizeLarge: {
                    padding: pxToRem(11) + ' ' + pxToRem(24),
                },
                textSizeSmall: {
                    padding: pxToRem(7) + ' ' + pxToRem(12),
                },
                textSizeMedium: {
                    padding: pxToRem(9) + ' ' + pxToRem(16),
                },
                textSizeLarge: {
                    padding: pxToRem(12) + ' ' + pxToRem(16),
                },
                // outlined: {
                // 	borderWidth: pxToRem(2),
                // },
            },
            variants: [
                {
                    props: { variant: 'mode' },
                    style: {
                        border: 0,
                        backgroundColor: palette.background.tile,
                        '&:hover': {
                            backgroundColor: palette.background.tileHover,
                        },
                    },
                },
                // {
                // 	props: { variant: 'dashed', color: 'secondary' },
                // 	style: {
                // 		border: `4px dashed rgba(0, 200, 0, 0.2)`,
                // 	},
                // },
            ],
        },
        MuiCard: {
            styleOverrides: {
                root: {
                    backgroundColor: palette.background,
                    padding: pxToRem(24),
                    borderRadius: 20,
                    [`&.${paperClasses.elevation1}`]: {
                        boxShadow: '0px 5px 22px rgba(0, 0, 0, 0.04), 0px 0px 0px 0.5px rgba(0, 0, 0, 0.03)',
                    },
                    '&::-webkit-scrollbar, & *::-webkit-scrollbar': {
                        borderRadius: 8,
                        backgroundColor: palette.neutral[200],
                    },
                    '&::-webkit-scrollbar-thumb, & *::-webkit-scrollbar-thumb': {
                        borderRadius: 8,
                        backgroundColor: palette.neutral[400],
                        minHeight: 30,
                        width: pxToRem(5),
                        border: '3px solid ' + palette.neutral[200],
                    },
                    '&::-webkit-scrollbar-thumb:focus, & *::-webkit-scrollbar-thumb:focus': {
                        backgroundColor: palette.neutral[400],
                    },
                    '&::-webkit-scrollbar-thumb:active, & *::-webkit-scrollbar-thumb:active': {
                        backgroundColor: palette.neutral[400],
                    },
                    '&::-webkit-scrollbar-thumb:hover, & *::-webkit-scrollbar-thumb:hover': {
                        backgroundColor: palette.neutral[400],
                    },
                    '&::-webkit-scrollbar-corner, & *::-webkit-scrollbar-corner': {
                        backgroundColor: palette.background,
                    },
                },
            },
        },
        MuiCardContent: {
            styleOverrides: {
                root: {
                    padding: pxToRem(32) + ' ' + pxToRem(24),
                    '&:last-child': {
                        paddingBottom: pxToRem(32),
                    },
                },
            },
        },
        MuiCardHeader: {
            defaultProps: {
                titleTypographyProps: {
                    variant: 'h6',
                },
                subheaderTypographyProps: {
                    variant: 'body2',
                },
            },
            styleOverrides: {
                root: {
                    padding: pxToRem(32) + ' ' + pxToRem(24) + ' ' + pxToRem(16),
                },
            },
        },
        MuiCssBaseline: {
            styleOverrides: {
                '*': {
                    boxSizing: 'border-box',
                },
                html: {
                    MozOsxFontSmoothing: 'grayscale',
                    WebkitFontSmoothing: 'antialiased',
                    display: 'flex',
                    flexDirection: 'column',
                    minHeight: '100vh',
                    width: '100%',
                },
                body: {
                    display: 'flex',
                    flex: '1 1 auto',
                    flexDirection: 'column',
                    minHeight: '100vh',
                    width: '100%',
                    overflowX: 'hidden',
                    backgroundColor: palette.background.default,
                    position: 'relative',
                    'div#root': {
                        content: '""',
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        background: `linear-gradient(30deg, ${palette.background.semi} 0%, ${palette.background.default} 40%, ${palette.background.default} 60%, ${palette.background.semi} 100%)`,
                    },
                },
                '#__next': {
                    display: 'flex',
                    flex: '1 1 auto',
                    flexDirection: 'column',
                    height: '100%',
                    width: '100%',
                },
                '#nprogress': {
                    pointerEvents: 'none',
                },
                '#nprogress .bar': {
                    backgroundColor: palette.primary.main,
                    height: 3,
                    left: 0,
                    position: 'fixed',
                    top: 0,
                    width: '100%',
                    zIndex: 2000,
                },
            },
        },
        MuiDialogTitle: {
            styleOverrides: {
                root: {
                    fontSize: 18,
                },
            },
        },
        MuiFilledInput: {
            styleOverrides: {
                root: {
                    backgroundColor: 'transparent',
                    borderRadius: 8,
                    borderStyle: 'solid',
                    borderWidth: 1,
                    overflow: 'hidden',
                    borderColor: palette.neutral[200],
                    transition: muiTheme.transitions.create(['border-color', 'box-shadow']),
                    '&:hover': {
                        [`& .${outlinedInputClasses.notchedOutline}`]: {
                            borderColor: palette.neutral[400],
                        },
                    },
                    '&:before': {
                        display: 'none',
                    },
                    '&:after': {
                        display: 'none',
                    },
                    [`&.${filledInputClasses.disabled}`]: {
                        backgroundColor: 'transparent',
                    },
                    [`&.${filledInputClasses.focused}`]: {
                        backgroundColor: 'transparent',
                        borderColor: palette.primary.main,
                    },
                    [`&.${filledInputClasses.error}`]: {
                        borderColor: palette.error.main,
                    },
                },
                input: {
                    fontSize: 14,
                    fontWeight: 500,
                    lineHeight: pxToRem(24),
                },
            },
        },
        MuiFormLabel: {
            styleOverrides: {
                root: {
                    fontSize: 14,
                    fontWeight: 500,
                    [`&.${inputLabelClasses.filled}`]: {
                        transform: 'translate(12px, 18px) scale(1)',
                    },
                    [`&.${inputLabelClasses.shrink}`]: {
                        [`&.${inputLabelClasses.standard}`]: {
                            transform: 'translate(0, -1.5px) scale(0.85)',
                        },
                        [`&.${inputLabelClasses.filled}`]: {
                            transform: 'translate(12px, 6px) scale(0.85)',
                        },
                        [`&.${inputLabelClasses.outlined}`]: {
                            transform: 'translate(14px, -9px) scale(0.85)',
                        },
                    },
                },
            },
        },
        MuiIconButton: {
            styleOverrides: {
                root: {
                    padding: '8px',
                    [`&.standalone`]: {
                        margin: '0 -8px !important',
                    },
                    [`&.${iconButtonClasses.sizeMedium}`]: {
                        svg: {
                            width: '2rem',
                            height: '2rem',
                        },
                    },
                    [`&.${iconButtonClasses.sizeLarge}`]: {
                        svg: {
                            width: '2.4rem',
                            height: '2.4rem',
                        },
                    },
                },
            },
        },
        MuiInput: {
            styleOverrides: {
                input: {
                    fontSize: 14,
                    fontWeight: 500,
                    lineHeight: pxToRem(24),
                    '&::placeholder': {
                        color: palette.text.secondary,
                    },
                },
            },
        },
        MuiInputBase: {
            styleOverrides: {
                input: {
                    '&::placeholder': {
                        opacity: 1,
                    },
                },
            },
        },
        MuiLinearProgress: {
            styleOverrides: {
                root: {
                    backgroundColor: palette.neutral[300],
                    borderColor: palette.neutral[300],
                    borderStyle: 'solid',
                    borderWidth: 0,
                    borderRadius: 8,
                    height: pxToRem(14),

                    [`.${linearProgressClasses.bar}`]: {
                        backgroundColor: palette.success.main,
                    },
                    [`.${linearProgressClasses.dashed}`]: {
                        backgroundImage: 'none',
                    },
                    [`.${linearProgressClasses.bar1Buffer}`]: {
                        backgroundColor: palette.success.light,
                        zIndex: 'inherit',
                    },
                    [`.${linearProgressClasses.bar2Buffer}`]: {
                        backgroundColor: palette.success.main,
                    },
                },
            },
        },
        MuiModal: {
            styleOverrides: {
                root: {
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                },
                backdrop: {
                    backgroundColor: palette.background.backDrop,
                },
            },
        },
        MuiOutlinedInput: {
            styleOverrides: {
                root: {
                    '&:hover': {
                        [`& .${outlinedInputClasses.notchedOutline}`]: {
                            borderColor: palette.neutral[400],
                        },
                    },
                    [`&.${outlinedInputClasses.focused}`]: {
                        backgroundColor: 'transparent',
                        [`& .${outlinedInputClasses.notchedOutline}`]: {
                            borderColor: palette.primary.main,
                        },
                    },
                    [`&.${filledInputClasses.error}`]: {
                        [`& .${outlinedInputClasses.notchedOutline}`]: {
                            borderColor: palette.error.main,
                        },
                    },
                },
                input: {
                    fontSize: 14,
                    fontWeight: 500,
                    lineHeight: pxToRem(24),
                },
                notchedOutline: {
                    borderColor: palette.neutral[200],
                    transition: muiTheme.transitions.create(['border-color']),
                },
            },
        },
        MuiPaper: {
            styleOverrides: {
                root: {
                    backgroundImage: 'none',
                    padding: `${pxToRem(10)} 0`,
                },
            },
        },
        MuiPopper: {
            styleOverrides: {
                root: {
                    '&::-webkit-scrollbar, & *::-webkit-scrollbar': {
                        borderRadius: 8,
                        backgroundColor: palette.background.input,
                    },
                    '&::-webkit-scrollbar-thumb, & *::-webkit-scrollbar-thumb': {
                        borderRadius: 8,
                        backgroundColor: palette.neutral[400],
                        minHeight: 30,
                        border: '3px solid ' + palette.background.input,
                    },
                    '&::-webkit-scrollbar-thumb:focus, & *::-webkit-scrollbar-thumb:focus': {
                        backgroundColor: palette.neutral[400],
                    },
                    '&::-webkit-scrollbar-thumb:active, & *::-webkit-scrollbar-thumb:active': {
                        backgroundColor: palette.neutral[400],
                    },
                    '&::-webkit-scrollbar-thumb:hover, & *::-webkit-scrollbar-thumb:hover': {
                        backgroundColor: palette.neutral[400],
                    },
                    '&::-webkit-scrollbar-corner, & *::-webkit-scrollbar-corner': {
                        backgroundColor: palette.background.input,
                    },
                },
            },
        },
        MuiRadio: {
            styleOverrides: {
                sizeSmall: {
                    padding: 4,
                },
            },
        },
        MuiSwitch: {
            styleOverrides: {
                root: ({ theme }) => {
                    return {
                        width,
                        height,
                        padding: 0,
                        margin: theme.spacing(1),
                        overflow: 'unset',
                        [`& .${switchClasses.switchBase}`]: {
                            padding: pxToRem(gap),
                            [`&.${switchClasses.checked}`]: {
                                color: '#fff',
                                transform: `translateX(calc(${width} - ${size} - ${pxToRem(2 * gap)}))`,
                                [`& + .${switchClasses.track}`]: {
                                    backgroundColor: theme.palette.primary.main,
                                    opacity: 1,
                                    border: 'none',
                                },
                                [`& .${switchClasses.thumb}`]: {
                                    backgroundColor: '#fff',
                                },
                            },
                        },
                        [`& .${switchClasses.thumb}`]: {
                            boxShadow: 'none',
                            backgroundColor: theme.palette.grey[400],
                            width: size,
                            height: size,
                        },
                        [`& .${switchClasses.track}`]: {
                            borderRadius: 40,
                            border: `solid ${theme.palette.grey[400]}`,
                            borderWidth,
                            backgroundColor: theme.palette.grey[200],
                            opacity: 1,
                            transition: theme.transitions.create(['background-color', 'border']),
                            boxSizing: 'border-box',
                        },
                    };
                },
            },
        },
        MuiTabs: {
            styleOverrides: {
                root: {
                    minHeight: 30,
                },
            },
        },
        MuiTab: {
            styleOverrides: {
                root: {
                    fontSize: 14,
                    fontWeight: 500,
                    lineHeight: 1.25,
                    minHeight: 30,
                    minWidth: 'auto',
                    padding: '4px 0',
                    textTransform: 'none',
                    '& + &': {
                        marginLeft: 20,
                    },
                },
            },
        },
        MuiTableCell: {
            styleOverrides: {
                root: {
                    borderBottomColor: palette.divider,
                },
            },
        },
        MuiTableHead: {
            styleOverrides: {
                root: {
                    borderBottom: 'none',
                    [`& .${switchClasses.root}`]: {
                        borderBottom: 'none',
                        backgroundColor: palette.neutral[50],
                        color: palette.neutral[700],
                        fontSize: 12,
                        fontWeight: 600,
                        lineHeight: 1,
                        letterSpacing: 0.5,
                        textTransform: 'uppercase',
                    },
                    [`& .${tableCellClasses.paddingCheckbox}`]: {
                        paddingTop: 4,
                        paddingBottom: 4,
                    },
                },
            },
        },
        MuiToggleButtonGroup: {
            styleOverrides: {
                root: ({ theme }) => {
                    return {
                        marginLeft: '-4px',
                        marginRight: '-4px',
                        [`& .${toggleButtonGroupClasses.grouped}:not(:first-of-type)`]: {
                            border: 0,
                            borderTopLeftRadius: theme.shape.borderRadius,
                            borderBottomLeftRadius: theme.shape.borderRadius,
                        },
                        [`& .${toggleButtonGroupClasses.grouped}:not(:last-of-type)`]: {
                            border: 0,
                            borderTopRightRadius: theme.shape.borderRadius,
                            borderBottomRightRadius: theme.shape.borderRadius,
                        },
                        // [`& .${toggleButtonClasses.disabled}`]: {
                        // },
                        // [`& .${toggleButtonClasses.selected}`]: {
                        // },
                        [`& .${toggleButtonGroupClasses.grouped}`]: {
                            margin: theme.spacing(0.5),
                            border: 0,
                            borderRadius: theme.shape.borderRadius,
                            [`&.${toggleButtonGroupClasses.disabled}`]: {
                                border: 0,
                            },
                        },
                        [`& .${toggleButtonGroupClasses.middleButton},& .${toggleButtonGroupClasses.lastButton}`]: {
                            borderRadius: theme.shape.borderRadius,
                        },
                    };
                },
            },
        },
        MuiToggleButton: {
            styleOverrides: {
                root: ({ theme }) => {
                    return {
                        lineHeight: 1.25,
                        border: 0,
                        backgroundColor: theme.palette.neutral[100],
                        color: theme.palette.neutral[400],
                        // [`&.${toggleButtonClasses.disabled}`]: {
                        // },
                        [`&:hover`]: {
                            color: theme.palette.secondary.contrast,
                        },
                        [`&.${toggleButtonClasses.selected}`]: {
                            color: theme.palette.secondary.contrast,
                            backgroundColor: theme.palette.secondary.alpha12,
                        },
                    };
                },
            },
        },
        // MuiTextField: {
        //     defaultProps: {
        //         variant: 'filled',
        //     },
        // },
        MuiTypography: {
            styleOverrides: {
                root: {
                    fontSize: 14,
                },
            },
        },
    };
}
