export const getEntityForKey = (_entityType, _key) => {
    const entity =
        _entityType === ENTITY_COUNTRY
            ? getCountries()[_key]
            : _entityType === ENTITY_LANDMARK
              ? getLandmarks().find((l) => l.key === _key)
              : _entityType === ENTITY_PEAK
                ? getPeaks().find((p) => p.key === _key)
                : _entityType === ENTITY_SEA
                  ? getSeasAndLakes()[_key]
                  : _entityType === ENTITY_CITY
                    ? getCities().find((c) => c.key === _key).country
                    : undefined;
    return entity;
};

export default configGeoGo;
