import { ReactComponent as Brain } from 'assets/images/brain.svg';
import { ReactComponent as Flag } from 'assets/images/flag1.svg';
import { ReactComponent as Landmark } from 'assets/images/landmark.svg';
import { ReactComponent as Location } from 'assets/images/location.svg';
import { ReactComponent as WorldMap } from 'assets/images/map.svg';
import { ReactComponent as Peak } from 'assets/images/peak.svg';
import { ReactComponent as Sea } from 'assets/images/sea.svg';
import { CAT_CAPITAL, CAT_FLAG, CAT_LANDMARK, CAT_MAP, CAT_PEAK, CAT_SEA, CAT_TRIVIA } from 'config/baseConfig';
import styled from 'styled-components';

import { getCountries } from 'data/data';

export const shuffle = (array) => {
    let currentIndex = array.length,
        randomIndex;

    // While there remain elements to shuffle.
    while (currentIndex > 0) {
        // Pick a remaining element.
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
    }

    return array;
};

export const pxToRem = (px) => {
    return px / 14 + 'rem';
};

export const pxToRemInt = (px) => {
    return px / 14;
};

export const getGameSVG = (mode) => {
    let svg;
    switch (mode) {
        case CAT_CAPITAL:
            svg = <Location />;
            break;
        case CAT_MAP:
            svg = <WorldMap />;
            break;
        case CAT_FLAG:
            svg = <Flag />;
            break;
        case CAT_LANDMARK:
            svg = <Landmark />;
            break;
        case CAT_PEAK:
            svg = <Peak />;
            break;
        case CAT_SEA:
            svg = <Sea />;
            break;
        case CAT_TRIVIA:
        default:
            svg = <Brain />;
            break;
    }
    return svg;
};

const StyledSpacer = styled.div`
    display: block;
    height: ${(props) => pxToRem(props.height)};
`;

export const Spacer = (props) => {
    return <StyledSpacer height={props.height} />;
};

export const getQuestionType = (_questionTypes, _key) => {
    return _questionTypes.find((qt) => qt.key === _key);
};

export const cacheAllAssets = () => {
    // download assets
    const countries = getCountries();
    Object.values(countries).forEach((country) => {
        if (country && country.cca3) {
            var img = new Image();
            img.src = `${process.env.PUBLIC_URL}/assets/flagsAndGeo/svg/${country.cca3.toLowerCase()}.svg`;
        }
    });
};

export const parseCoordinates = (coordString) => {
    // Regular expression to match DMS format (handles missing seconds)
    const dmsRegex = /([0-9]+)\D+([0-9]+)?\D*([0-9.]+)?\D*([NSEW])/gi;
    // Regular expression to match decimal degrees format
    const decimalRegex = /([-+]?[0-9]*\.?[0-9]+)\D*\u00B0?\D*([NSEW])/gi;

    let coordinates = [];
    let match;

    // Try to match DMS format first
    while ((match = dmsRegex.exec(coordString)) !== null) {
        let degrees = parseFloat(match[1]);
        let minutes = match[2] ? parseFloat(match[2]) : 0;
        let seconds = match[3] ? parseFloat(match[3]) : 0;
        let direction = match[4];

        let decimal = degrees + minutes / 60 + seconds / 3600;
        if (direction === 'S' || direction === 'W') {
            decimal = -decimal;
        }
        coordinates.push(decimal);
    }

    // If no DMS matches, try decimal degrees format
    if (coordinates.length === 0) {
        while ((match = decimalRegex.exec(coordString)) !== null) {
            let value = parseFloat(match[1]);
            let direction = match[2];

            if (direction === 'S' || direction === 'W') {
                value = -value;
            }
            coordinates.push(value);
        }
    }

    return coordinates.length === 2 ? coordinates : null;

    // // Example usage:
    // console.log(parseCoordinates("41°19′38″N 19°49′07″E")); // [41.3272, 19.8186]
    // console.log(parseCoordinates("18.23033°N 63.04333°W")); // [18.23033, -63.04333]
    // console.log(parseCoordinates("60°06′N 19°56′E")); // [60.1, 19.9333]
    // console.log(parseCoordinates("42.70°N 23.33°E")); // [42.7, 23.33]
};
