import { GameMode } from 'store/quizBuilder/gameModes/GameMode';

export class GameModePeak extends GameMode {
    filterEntities() {
        if (this.isNormal) {
            this.entities.filteredEntities = this.entities.filteredEntities.filter(
                (peak) => peak.prominence > 2500 || peak.include
            );
        } else if (this.isHard) {
            this.entities.filteredEntities = this.entities.filteredEntities.filter(
                (peak) => peak.prominence > 1000 || peak.include
            );
        }
        if (this.mode.questionType.questionProperty === 'images') {
            this.entities.filteredEntities = this.entities.filteredEntities.filter(
                (peak) => peak.images && peak.images?.length > 0
            );
        }
        super.filterEntities();
    }
}
