import { t } from 'i18next';

import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeLandmarkCountry extends QuestionType {
    getQuestion(_entity) {
        const questionText = this.getQuestionValue(_entity);
        const questionAdditionnalText = _entity.getName(this.language);
        const allLdmrkC = this.shuffle(this.getAnswer(_entity));
        const choices = this.getChoices(_entity, allLdmrkC, this.getChoicesList());
        const answer = allLdmrkC.pop();
        const answerAdditionnalText = this.getAdditionnalText(_entity, allLdmrkC);
        return { questionText, questionAdditionnalText, answer, choices, answerAdditionnalText };
    }

    getAdditionnalText(_entity, _allLdmrkC) {
        return _allLdmrkC && _allLdmrkC.length > 0 ? `${t('and')} ` + _allLdmrkC.join(', ') : undefined;
    }
}
