import { NB_CHOICES, difficultyLevels } from 'config/baseConfig';
import { ROUTE_HOME, ROUTE_TEST } from 'config/routes';
import ReactGA from 'react-ga4';

import configGeoGo from 'data/ConfigGeoGo';
import * as constants from 'store/constants';

// ACTIONS
export const setConfig = (_location) => {
    let config;
    switch (_location.pathname) {
        case ROUTE_HOME:
            config = configGeoGo;
            break;
        case ROUTE_TEST:
        default:
            config = configGeoGo;
            break;
    }
    return {
        type: constants.INIT_CONFIG,
        gameConfig: { ...config, nbChoices: NB_CHOICES, difficultyLevels: difficultyLevels },
    };
};

export const editSettings = (_settingsData) => {
    return {
        type: constants.EDIT_SETTINGS,
        settings: _settingsData,
    };
};

export const setIsInit = (_isInit) => {
    ReactGA.send({ hitType: 'pageview', page: '/home', title: 'Home' });
    return {
        type: constants.IS_INIT,
        isInit: _isInit,
    };
};

export const setPage = (_page) => {
    if (_page === 'wiki') {
        ReactGA.send({ hitType: 'pageview', page: '/wiki', title: 'Wiki' });
    } else {
        ReactGA.send({ hitType: 'pageview', page: '/home', title: 'Home' });
    }
    return {
        type: constants.SET_PAGE,
        page: _page,
    };
};

export const newGame = () => {
    ReactGA.send({ hitType: 'pageview', page: '/home', title: 'Home' });
    return {
        type: constants.NEW_GAME,
    };
};

export const chooseGameCategory = (_chosenGameCategory) => {
    return {
        type: constants.CHOOSE_GAMECATEGORY,
        chosenGameCategory: _chosenGameCategory,
    };
};
export const chooseGameMode = (_chosenGameMode) => {
    return {
        type: constants.CHOOSE_GAMEMODE,
        chosenGameMode: _chosenGameMode,
    };
};

export const startGame = (
    _chosenGameMode,
    _chosenRegion,
    _chosenDifficultyLevel,
    _chosenIndependantOnly,
    _chosenGameLength,
    _chosenHideBackgroundMap
) => {
    ReactGA.event({
        category: 'game',
        action: 'start',
        label: `${_chosenGameMode}-${_chosenRegion}-${_chosenDifficultyLevel}`, // optional
        //   value: 99, // optional, must be a number
        //   nonInteraction: true, // optional, true/false
        //   transport: "xhr", // optional, beacon/xhr/image
    });
    ReactGA.event({
        category: 'config',
        action: 'config_mode',
        label: _chosenGameMode,
    });
    ReactGA.event({
        category: 'config',
        action: 'config_region',
        label: _chosenRegion,
    });
    ReactGA.event({
        category: 'config',
        action: 'config_difficulty',
        label: _chosenDifficultyLevel,
    });
    // ReactGA.event({
    //     category: 'config',
    //     action: 'config_independant',
    //     label: _chosenIndependantOnly,
    // });
    ReactGA.event({
        category: 'config',
        action: 'config_length',
        label: _chosenGameLength,
    });
    return {
        type: constants.START_GAME,
        chosenGameMode: _chosenGameMode,
        chosenRegion: _chosenRegion,
        chosenDifficultyLevel: _chosenDifficultyLevel,
        chosenIndependantOnly: _chosenIndependantOnly,
        chosenGameLength: _chosenGameLength,
        chosenHideBackgroundMap: _chosenHideBackgroundMap,
    };
};

export const reStartGame = (
    _chosenGameMode,
    _chosenRegion,
    _chosenDifficultyLevel,
    _chosenIndependantOnly,
    _chosenGameLength,
    _chosenHideBackgroundMap
) => {
    ReactGA.event({
        category: 'game',
        action: 'restart',
        label: `${_chosenGameMode}-${_chosenRegion}-${_chosenDifficultyLevel}`, // optional
    });
    return {
        type: constants.START_GAME,
        chosenGameMode: _chosenGameMode,
        chosenRegion: _chosenRegion,
        chosenDifficultyLevel: _chosenDifficultyLevel,
        chosenIndependantOnly: _chosenIndependantOnly,
        chosenGameLength: _chosenGameLength,
        chosenHideBackgroundMap: _chosenHideBackgroundMap,
    };
};

export const gameNewQuestion = () => {
    return {
        type: constants.GAME_NEW_QUESTION,
    };
};

export const gameNext = () => {
    return {
        type: constants.GAME_NEXT,
    };
};

export const gameAnswer = (_chosenAnswer) => {
    return {
        type: constants.GAME_ANSWER,
        chosenAnswer: _chosenAnswer,
    };
};
