import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeCityMap extends QuestionType {
    getQuestion(_entity) {
        if (this.isNormal && !_entity.isCapital) {
            return undefined;
        }
        return super.getQuestion(_entity);
    }
    canBeInChoices(
        _ignoreDifficulty,
        _rightAnswer,
        choices,
        indexUsed,
        rnd,
        _entity,
        tmpChoice,
        tmpChoiceValue,
        expertChoices
    ) {
        let otherCondition = true;
        const distance = _entity.getDistanceFromLatLonInKm(tmpChoice.coordinates);
        if (this.isNormal) {
            otherCondition = tmpChoice.isCapital && _entity.countryCCA3 !== tmpChoice.countryCCA3 && distance > 1000;
        } else if (this.isHard) {
            otherCondition = distance > 500;
        } else {
            otherCondition = distance > 100;
        }

        return (
            otherCondition &&
            super.canBeInChoices(
                _ignoreDifficulty,
                _rightAnswer,
                choices,
                indexUsed,
                rnd,
                _entity,
                tmpChoice,
                tmpChoiceValue,
                expertChoices
            )
        );
    }
}
