import { GameMode } from 'store/quizBuilder/gameModes/GameMode';

export class GameModeSeas extends GameMode {
    filterEntities() {
        if (this.mode.questionType.questionProperty === 'seas_largest') {
            this.entities.filteredEntities = this.entities.filteredEntities.filter((sea) => !!sea.area);
        }
        super.filterEntities();
    }
}
