import { common } from '@mui/material/colors';
import { alpha } from '@mui/material/styles';

import { error, info, neutral, neutralsWithAlphas, purple, secondary, success, warning } from 'theme/colors';

const neutralLight = neutralsWithAlphas(neutral);

export function createPalette() {
    return {
        action: {
            active: neutralLight[500],
            disabled: alpha(neutralLight[900], 0.38),
            disabledSelected: alpha(neutralLight[900], 0.56),
            disabledBackground: alpha(neutralLight[900], 0.12),
            focus: alpha(neutralLight[900], 0.16),
            hover: alpha(neutralLight[900], 0.04),
            selected: alpha(neutralLight[900], 0.12),
        },
        background: {
            default: secondary.lightest,
            input: neutralLight[50],
            paper: neutralLight[50],
            tile: neutralLight[50],
            tileHover: '#e9eef7',
            semi: neutralLight[50],
            rightAnswer: secondary.main,
            tableRow: neutralLight[100],
            backDrop: 'rgba(0,0,0,0.85)',
        },
        divider: neutralLight[100],
        error,
        info,
        mode: 'light',
        neutral: neutralLight,
        primary: { ...purple, lightContrast: common.white },
        secondary: { ...secondary, lightContrast: common.white },
        success,
        text: {
            title: common.black,
            main: neutralLight[700],
            primary: neutralLight[700],
            secondary: neutralLight[500],
            disabled: alpha(neutralLight[900], 0.38),
            attribution: '#fff',
        },
        warning,
        shadow: neutralLight[1000],
    };
}
