import { ENTITY_PEAK } from 'config/baseConfig';

import { getCountries } from 'data/data';

export class Peak {
    constructor(obj) {
        this.isPeak = true;
        this.entityType = ENTITY_PEAK;

        this.key = obj.name;
        this.name = obj.name;
        this.altername_names = obj.altername_names?.split(',').join(', ');
        this.range_or_island = obj.range_or_island;
        this.country = obj.country.split(',').map((c) => getCountries()[c]);

        this.prominence = obj.prominence;
        this.elevation = obj.elevation;
        this.images = obj.images;
        this.coord = obj.coord;
    }

    getName(lang) {
        return lang === 'eng' ? this.name : this.name.replace('Mount', 'Mont');
        // return this.translations[lang].common;
    }
    getRange(lang) {
        return this.range_or_island;
        // return this.translations[lang].range_or_island;
    }
    getRegion(lang) {
        return this.country
            .map((c) => (c.subregion ? c.subregion : c.region)[lang])
            .filter((value, index, array) => array.indexOf(value) === index)
            .join(', ');
    }
    get regionCode() {
        return this.country?.[0].regionCode;
    }
    get subregionCode() {
        return this.country?.[0].subregionCode;
    }

    getValue(lang, valueName, nbFromList) {
        let value;
        switch (valueName) {
            case 'name':
                value = this.getName(lang);
                break;
            case 'elevation':
                value = this.elevation;
                break;
            case 'range':
                value = this.getRange(lang);
                break;
            case 'country':
                value = this.country.map((c) => c.getName(lang));
                // value = nbFromList === 1 ? this.getOneRandom(country) : country;
                break;
            default:
                value = this[valueName];
                break;
        }
        // console.log(value);
        return value;
    }

    isInRegion(region) {
        return !!this.country.find((c) => c.regionCode === region);
    }
}
