import { t } from 'i18next';

import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeTheMostSomething extends QuestionType {
    getQuestion(_entity) {
        const questionText = this.getQuestionValue(_entity);
        let answer = this.getAnswer(_entity);
        if (!answer) {
            return undefined;
        }
        const choices = this.getChoices(_entity, answer, undefined);
        answer = {
            value: answer,
            label: answer,
            // additionalLabel: _entity.country.map((c) => c.getName(this.language)).join(', '),
        };
        const answerAdditionnalText = this.getAdditionnalText(_entity);
        return { questionText, answer, choices, answerAdditionnalText };
    }

    getChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty = false) {
        if (!_rightAnswer || _rightAnswer.length === 0 || this.isFlashcard) {
            return undefined;
        }
        return this.generateChoices(
            _entity,
            _rightAnswer,
            this.gameMode.getEntities(this.questionType.entityType),
            _ignoreDifficulty
        );
    }

    getAdditionnalText(_entity) {
        return `${_entity.getValue(this.language, this.questionType.compareProperty)} ${t(this.questionType.unit)}`;
    }

    generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty = false) {
        const choices = [];
        const indexUsed = [];

        const entityVal = _entity.getValue(this.language, this.questionType.compareProperty);
        if (!entityVal) {
            // console.log('unavailable questionType for entity', _entity, entityVal);
            // console.trace();
            return undefined;
        }
        // console.log('yaaaa', _entity, entityVal);

        let minDiffHeight = this.questionType.diffs[this.gameMode.difficultyLevel][0];
        let maxDiffHeight = this.questionType.diffs[this.gameMode.difficultyLevel][1];

        minDiffHeight = minDiffHeight ? minDiffHeight * entityVal : undefined;
        maxDiffHeight = maxDiffHeight ? maxDiffHeight * entityVal : undefined;

        const choicesFrom = _possibleChoicesPerRegion.filter((p) => {
            const pVal = p.getValue(this.language, this.questionType.compareProperty);

            return this.questionType.compareMost
                ? (!minDiffHeight || entityVal - pVal > minDiffHeight) &&
                      (!maxDiffHeight || entityVal - pVal < maxDiffHeight)
                : (!minDiffHeight || pVal - entityVal > minDiffHeight) &&
                      (!maxDiffHeight || pVal - entityVal < maxDiffHeight);
        });

        const max = choicesFrom.length;
        let nbTry = 0;
        while (choices.length < this.nbChoices - 1) {
            nbTry++;
            if (nbTry > 20) {
                // console.log('while broke', _entity, choices, entityVal);
                return undefined;
            }
            const rnd = Math.floor(Math.random() * max);
            if (indexUsed.includes(rnd) || typeof choicesFrom[rnd] === 'undefined') {
                continue;
            }

            choices.push({
                value: choicesFrom[rnd].getName(this.language),
                label: choicesFrom[rnd].getName(this.language),
                // additionalLabel: choicesFrom[rnd].country.map((c) => c.getName(this.language)).join(', '),
            });
            indexUsed.push(rnd);
        }

        return choices.length === this.nbChoices - 1 ? choices : undefined;
    }
}
