import { ENTITY_CITY, ENTITY_COUNTRY, ENTITY_LANDMARK, ENTITY_PEAK, ENTITY_SEA } from 'config/baseConfig';
import { DIFFICULTY_FLASHCARD } from 'config/baseConfig';

import { GameMode } from 'store/quizBuilder/gameModes/GameMode';
import { GameModeCountry } from 'store/quizBuilder/gameModes/GameModeCountry';
import { GameModePeak } from 'store/quizBuilder/gameModes/GameModePeak';
import { GameModeSeas } from 'store/quizBuilder/gameModes/GameModeSeas';
import { GameModeTrivia } from 'store/quizBuilder/gameModes/GameModeTrivia';

export class QuizBuilder {
    constructor(_gameConfig) {
        this.gameConfig = _gameConfig;
    }

    generateQuestions(language, mode, chosenRegion, chosenDifficulty, chosenIndependantOnly, chosenGameLength) {
        const gameModeClasses = {};
        gameModeClasses[ENTITY_COUNTRY] = GameModeCountry;
        gameModeClasses[ENTITY_CITY] = GameMode;
        gameModeClasses[ENTITY_LANDMARK] = GameMode;
        gameModeClasses[ENTITY_PEAK] = GameModePeak;
        gameModeClasses[ENTITY_SEA] = GameModeSeas;
        let gameModeClass;
        if (mode.isTrivia) {
            gameModeClass = GameModeTrivia;
        } else {
            if (Array.isArray(mode.questionType)) {
                gameModeClass = gameModeClasses[mode.questionType[0].entityType];
            } else {
                gameModeClass = gameModeClasses[mode.questionType.entityType];
            }
        }

        const gameMode = new gameModeClass(
            this.gameConfig,
            mode,
            chosenRegion,
            chosenDifficulty,
            chosenIndependantOnly,
            language
        );
        // const reducedFilteredEntities = gameMode.generateQuizEntities(chosenGameLength);
        // // console.log(reducedFilteredEntities);

        // let questions = reducedFilteredEntities.map((entity) => {
        //     // console.log(entity);
        //     return gameMode.getQuestion(entity);
        // });

        let questions = gameMode.generateQuestions(chosenGameLength);
        questions = questions.filter(
            (q) =>
                q !== undefined &&
                q.answer !== undefined &&
                (q.choices !== undefined || chosenDifficulty === DIFFICULTY_FLASHCARD)
        );
        // console.log(questions.map((q) => q.questionTypeKey));
        // console.log(questions);

        return questions;
    }

    testAll(language) {
        Object.values(this.gameConfig.gameModes).forEach((mode) => {
            if (mode.isTrivia) {
                return;
            }
            this.gameConfig.difficultyLevels.forEach((difficultyLevel) => {
                if (
                    (difficultyLevel.key === DIFFICULTY_FLASHCARD && mode.hasFlashCardDifficulty) ||
                    difficultyLevel.key !== DIFFICULTY_FLASHCARD
                ) {
                    const questions = this.generateQuestions(
                        language,
                        mode,
                        'all',
                        difficultyLevel.key,
                        true && mode.canChooseIndependant,
                        'full'
                    );
                    // console.log(questions);
                    let questionsRegion;
                    if (mode.canChooseRegion) {
                        questionsRegion = this.generateQuestions(
                            language,
                            mode,
                            mode.canChooseRegion ? 'EU' : 'all',
                            difficultyLevel.key,
                            true && mode.canChooseIndependant,
                            'short'
                        );
                    }
                    console.log(
                        mode,
                        'all',
                        difficultyLevel.key,
                        true && mode.canChooseIndependant,
                        'full then short',
                        questions.length,
                        questionsRegion?.length
                    );
                    // console.log(questionsRegion);
                }
            });
        });
        const triviaMode = this.gameConfig.gameModes['trivia'];
        this.gameConfig.difficultyLevels.forEach((difficultyLevel) => {
            if (difficultyLevel.key !== DIFFICULTY_FLASHCARD) {
                const questions = this.generateQuestions(
                    language,
                    triviaMode,
                    'all',
                    difficultyLevel.key,
                    false,
                    'full'
                );
                console.log(triviaMode, 'all', difficultyLevel.key, false, 'full', questions.length);
                console.log(questions);
            }
        });
    }
}
