import { useSelector } from 'react-redux';

// SELECTOR HOOKS
export const useSettings = () => {
    return useSelector((state) => {
        return state.settings;
    });
};
export const useIsDarkMode = () => {
    return useSelector((state) => {
        return state.settings.isDarkMode;
    });
};
export const useShowAdvancedOptions = () => {
    return useSelector((state) => {
        return state.settings.showAdvancedOptions;
    });
};
export const useLanguage = () => {
    return useSelector((state) => {
        return state.settings.language;
    });
};
export const useIsInit = () => {
    return useSelector((state) => {
        return state.isInit;
    });
};
export const usePage = () => {
    return useSelector((state) => {
        return state.page;
    });
};
export const useCurrentGame = () => {
    return useSelector((state) => {
        return state.currentGame;
    });
};
export const useGameOptions = () => {
    return useSelector((state) => {
        return state.gameOptions;
    });
};
export const useLastGameCategory = () => {
    return useSelector((state) => {
        return state.lastGameModes?.['main'];
    });
};
export const useLastGameMode = () => {
    return useSelector((state) => {
        return state.lastGameModes?.['main'] ? state.lastGameModes[state.lastGameModes?.['main']] : undefined;
    });
};
export const useGameConfig = () => {
    return useSelector((state) => {
        return state.gameConfig;
    });
};
export const useCurrentGameMode = () => {
    return useSelector((state) => {
        if (state.currentGame) {
            return state.gameConfig?.gameModes[state.currentGame.gameMode];
        }
        return undefined;
    });
};
export const useGameCategory = () => {
    const gameMode = useCurrentGameMode();
    return useSelector((state) => {
        if (state.currentGame) {
            return state.gameConfig?.gameCategories.find((gc) => {
                return gc.modes.some((m) => m.key === gameMode.key);
            });
        }
        return undefined;
    });
};
