import { ENTITY_CITY } from 'config/baseConfig';

import { getCountries } from 'data/data';
import { parseCoordinates } from 'util/util';

export class City {
    constructor(obj) {
        this.isCity = true;
        this.entityType = ENTITY_CITY;

        this.key = obj.name.eng;
        this.name = obj.name;
        this.isCapital = obj.isCapital;
        this.countryCCA3 = obj.country;
        // this.country = countries[obj.country];

        if (obj.isCapital) {
            getCountries()[obj.country].addCapital(this);
        } else {
            getCountries()[obj.country].addCity(this);
        }

        this.coordinates = obj.coordinates;
    }
    get country() {
        return getCountries()[this.countryCCA3];
    }
    get independent() {
        return this.country?.independent;
    }
    get regionCode() {
        return this.country?.regionCode;
    }
    get subregionCode() {
        return this.country?.subregionCode;
    }

    getName(lang) {
        return this.name[lang];
    }

    getValue(lang, valueName, nbFromList) {
        let value;
        switch (valueName) {
            case 'name':
                value = this.getName(lang);
                break;
            case 'country':
                value = getCountries()[this.countryCCA3];
                break;
            case 'countryName':
                value = getCountries()[this.countryCCA3].getName(lang);
                break;
            default:
                value = this[valueName];
                break;
        }
        // console.log(value);
        return value;
    }

    isInRegion(region) {
        return this.regionCode === region;
    }

    getDistanceFromLatLonInKm(_otherCoordinates) {
        const latLon1 = parseCoordinates(this.coordinates);
        const lat1 = latLon1[0];
        const lon1 = latLon1[1];

        const latLon2 = parseCoordinates(_otherCoordinates);
        const lat2 = latLon2[0];
        const lon2 = latLon2[1];

        var R = 6371; // Radius of the earth in km
        var dLat = this.deg2rad(lat2 - lat1); // deg2rad below
        var dLon = this.deg2rad(lon2 - lon1);
        var a =
            Math.sin(dLat / 2) * Math.sin(dLat / 2) +
            Math.cos(this.deg2rad(lat1)) * Math.cos(this.deg2rad(lat2)) * Math.sin(dLon / 2) * Math.sin(dLon / 2);
        var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
        var d = R * c; // Distance in km
        return d;
    }

    deg2rad(deg) {
        return deg * (Math.PI / 180);
    }
}
