import React from 'react';

import { Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';

import MainButton from 'components/ui/MainButton';
import { pxToRem } from 'util/util';

const ConfirmDialog = (props) => {
    const closeRef = React.useRef < HTMLElement > null;
    const handleEntering = () => {
        if (closeRef.current != null) {
            closeRef.current.focus();
        }
    };

    return (
        <Dialog open={props.open} TransitionProps={{ onEntering: handleEntering }} aria-labelledby="quit-dialog-title">
            <DialogTitle id="quit-dialog-title">{props.title}</DialogTitle>
            {props.text ? (
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">{props.text}</DialogContentText>
                </DialogContent>
            ) : null}
            <DialogActions
                sx={{
                    p: pxToRem(12),
                    justifyContent: 'center',
                }}
            >
                <MainButton
                    buttonP={{
                        color: 'error',
                        onClick: props.leftHandler,
                    }}
                    typoP={{
                        fontSize: pxToRem(16),
                        fontWeight: '600',
                    }}
                >
                    {props.leftLabel}
                </MainButton>
                <MainButton
                    buttonP={{
                        // variant: 'outlined',
                        color: 'success',
                        onClick: props.rightHandler,
                    }}
                    typoP={{
                        fontSize: pxToRem(16),
                        fontWeight: '600',
                    }}
                >
                    {props.rightLabel}
                </MainButton>
            </DialogActions>
        </Dialog>
    );
};

export default ConfirmDialog;
