import { ENTITY_COUNTRY } from 'config/baseConfig';

import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeLanguageCountry extends QuestionType {
    getQuestion(_entity) {
        const questionText = this.getQuestionValue(_entity);
        const countryLang = this.shuffle(Object.keys(_entity.languages))?.[0];
        if (!countryLang) {
            return;
        }
        const answer = _entity.languages[countryLang];
        const langNamePerLanguages = this.getSpecialChoicesList(
            'langNamePerLanguages',
            this.generateLangNamePerLanguages.bind(this)
        );
        const choices = this.getLanguageChoices(_entity, answer, this.getChoicesList(), langNamePerLanguages);

        return { questionText, answer, choices };
    }

    // getLanguageChoices
    getLanguageChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _languagesNamePerLanguages) {
        const choices = [];
        const indexUsed = [];
        let answersFrom = _possibleChoicesPerRegion;

        if (this.isHard) {
            let answersFromRegion = _possibleChoicesPerRegion[_entity.regionCode][_entity.subregionCode];
            if (answersFromRegion.length < 7) {
                let allFromRegion = [];
                Object.keys(_possibleChoicesPerRegion[_entity.regionCode]).forEach((reg) => {
                    if (reg !== _entity.subregionCode) {
                        allFromRegion = allFromRegion.concat(_possibleChoicesPerRegion[_entity.regionCode][reg]);
                    }
                }, []);
                if (_entity.regionCode === 'AN') {
                    Object.keys(_possibleChoicesPerRegion['OC']).forEach((reg) => {
                        allFromRegion = allFromRegion.concat(_possibleChoicesPerRegion['OC'][reg]);
                    }, []);
                }
                answersFrom = answersFromRegion.concat(
                    this.shuffle(allFromRegion).slice(0, 7 - answersFromRegion.length)
                );
            } else {
                answersFrom = answersFromRegion;
            }
        }
        const max = answersFrom.length;
        let langKey;

        let nbTry = 0;
        while (choices.length < this.nbChoices - 1) {
            nbTry++;
            if (nbTry > 10) {
                // console.log('while broke', _entity, choices);
                return undefined;
            }

            const rnd = Math.floor(Math.random() * max);
            langKey = answersFrom[rnd];
            if (
                _languagesNamePerLanguages[langKey] === _rightAnswer || // answer _entity has the language
                _entity.languages[langKey] !== undefined || // answer _entity has the language
                indexUsed.includes(rnd) || // already used
                choices.includes(_languagesNamePerLanguages[langKey]) // already in choices
            ) {
                continue;
            }
            choices.push(_languagesNamePerLanguages[langKey]);
            indexUsed.push(rnd);
        }

        return choices;
    }

    generateLangNamePerLanguages() {
        let countriesPerLanguages = [];
        let languagesNamePerLanguages = {};
        this.gameMode.getEntities(ENTITY_COUNTRY).forEach((entity) => {
            if (entity.languages) {
                Object.keys(entity.languages).forEach((l) => {
                    if (!countriesPerLanguages[l]) {
                        countriesPerLanguages[l] = [];
                        languagesNamePerLanguages[l] = entity.languages[l];
                    }
                    countriesPerLanguages[l].push(entity);
                });
            }
        });
        return languagesNamePerLanguages;
    }
}
