import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeIsOrNot extends QuestionType {
    getQuestion(_entity) {
        if (_entity.getValue(this.language, this.questionType.choiceFilterProp) === this.questionType.choiceFilterVal) {
            return null;
        }
        const answer = this.getAnswer(_entity);
        const choices = this.getChoices(_entity, answer, this.getChoicesList());
        return { answer, choices };
    }

    canBeInChoices(
        _ignoreDifficulty,
        _rightAnswer,
        choices,
        indexUsed,
        rnd,
        _entity,
        tmpChoice,
        tmpChoiceValue,
        expertChoices
    ) {
        let otherCondition =
            _entity.getValue(this.language, this.questionType.choiceFilterProp) !==
            tmpChoice.getValue(this.language, this.questionType.choiceFilterProp);

        return (
            otherCondition &&
            super.canBeInChoices(
                _ignoreDifficulty,
                _rightAnswer,
                choices,
                indexUsed,
                rnd,
                _entity,
                tmpChoice,
                tmpChoiceValue,
                expertChoices
            )
        );
    }
}
