import { getCountries } from 'data/data';
import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeHasNBorder extends QuestionType {
    getQuestion(_entity) {
        const subsetCountries = this.getChoicesList().filter(
            (_c) =>
                _c.borders.length !== _entity.borders.length &&
                _c.borders.length >= _entity.borders.length - 2 &&
                _c.borders.length <= _entity.borders.length + 2
        );
        if (subsetCountries.length < 3) {
            return;
        }
        // console.log(subsetCountries);
        const questionText = this.getQuestionValue(_entity);
        const answer = this.getAnswer(_entity);
        const choices = this.getChoices(_entity, answer, subsetCountries, true);
        const questionPhraseValues = [_entity.borders.length, _entity.borders.length >= 2 ? 's' : ''];
        const answerAdditionnalText = this.getAdditionnalText(_entity);

        return { questionText, answer, choices, answerAdditionnalText, questionPhraseValues };
    }

    getAdditionnalText(_entity) {
        return _entity.borders.map((b) => getCountries()[b].getName(this.language)).join(', ');
    }
}
