import { QuestionTypeCityMap } from '../questionTypes/QuestionTypeCityMap';
import { QuestionTypeFlag } from '../questionTypes/QuestionTypeFlag';
import { ENTITY_CITY, ENTITY_COUNTRY, ENTITY_LANDMARK, ENTITY_PEAK, ENTITY_SEA } from 'config/baseConfig';
import {
    DIFFICULTY_EXPERT,
    DIFFICULTY_FLASHCARD,
    DIFFICULTY_HARD,
    DIFFICULTY_NORMAL,
    NB_QUESTIONS_SHORT,
    SHORT,
} from 'config/baseConfig';

import { getCities, getCountries, getLandmarks, getPeaks, getSeasAndLakes } from 'data/data';
import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';
import { QuestionTypeCapital } from 'store/quizBuilder/questionTypes/QuestionTypeCapital';
import { QuestionTypeHasNBorder } from 'store/quizBuilder/questionTypes/QuestionTypeHasNBorder';
import { QuestionTypeIsOrNot } from 'store/quizBuilder/questionTypes/QuestionTypeIsOrNot';
import { QuestionTypeIsOrNotCapital } from 'store/quizBuilder/questionTypes/QuestionTypeIsOrNotCapital';
import { QuestionTypeLandmarkCountry } from 'store/quizBuilder/questionTypes/QuestionTypeLandmarkCountry';
import { QuestionTypeLanguage } from 'store/quizBuilder/questionTypes/QuestionTypeLanguage';
import { QuestionTypeLanguageCountry } from 'store/quizBuilder/questionTypes/QuestionTypeLanguageCountry';
import { QuestionTypeNameOfficial } from 'store/quizBuilder/questionTypes/QuestionTypeNameOfficial';
import { QuestionTypeNameOfficialWrong } from 'store/quizBuilder/questionTypes/QuestionTypeNameOfficialWrong';
import { QuestionTypeShareBorder } from 'store/quizBuilder/questionTypes/QuestionTypeShareBorder';
import { QuestionTypeTheMostSomething } from 'store/quizBuilder/questionTypes/QuestionTypeTheMostSomething';
import { shuffle as _shuffle, getQuestionType } from 'util/util';

// this should be gameModeGeogo
export class GameMode {
    constructor(_gameConfig, _mode, _region, _difficultyLevel, _independantOnly, _language) {
        this.gameConfig = _gameConfig;
        this.mode = _mode;
        this.region = _region;
        this.difficultyLevel = _difficultyLevel;
        this.independantOnly = _independantOnly;
        this.language = _language;

        this.entities = {};
        this.choicesLists = {};

        this.generateEntities();
        this.filterEntities();

        this.questionTypesClasses = {
            capital: QuestionTypeCapital,
            country_capital: QuestionType,
            country_city: QuestionType,

            flag: QuestionTypeFlag,
            country_flag: QuestionTypeFlag,

            country_map: QuestionType,
            city_map: QuestionTypeCityMap,

            picture_landmark: QuestionType,
            country_landmark: QuestionTypeLandmarkCountry,

            country_peak: QuestionType,
            picture_peak: QuestionType,
            peak_highest: QuestionTypeTheMostSomething,

            sea_map: QuestionType,
            country_sea: QuestionType,
            sea_largest: QuestionTypeTheMostSomething,

            country_largest: QuestionTypeTheMostSomething,

            is_capital: QuestionTypeIsOrNotCapital,
            not_capital: QuestionTypeIsOrNotCapital,

            name_official: QuestionTypeNameOfficial,
            name_official_good: QuestionTypeNameOfficial,
            name_official_wrong: QuestionTypeNameOfficialWrong,

            share_border: QuestionTypeShareBorder,
            share_no_border: QuestionTypeShareBorder,
            has_n_border: QuestionTypeHasNBorder,

            is_landlocked: QuestionTypeIsOrNot,
            not_landlocked: QuestionTypeIsOrNot,

            language: QuestionTypeLanguage,
            language_country: QuestionTypeLanguageCountry,
        };

        this.questionTypesInstances = {};
    }

    generateEntities() {
        const qType = this.getQuestionType();
        this.entities.filteredEntities =
            this.entities.allEntities =
            this.entities[qType.entityType] =
                this.generateEntitiesForType(qType.entityType);

        if (qType.entityType !== qType.choiceEntityType) {
            this.entities[qType.choiceEntityType] = this.generateEntitiesForType(qType.choiceEntityType);
        }
    }

    getQuestionType() {
        return Array.isArray(this.mode.questionType)
            ? this.getOneRandom(this.mode.questionType)
            : this.mode.questionType;
    }

    // todo: GeoGoConfig devrait contenir tout ça
    generateEntitiesForType(_entityType) {
        // console.log(_entityType);
        let entities;
        switch (_entityType) {
            case ENTITY_PEAK:
                entities = Object.values(getPeaks());
                break;
            case ENTITY_LANDMARK:
                entities = Object.values(getLandmarks());
                break;
            case ENTITY_SEA:
                entities = Object.values(getSeasAndLakes());
                break;
            case ENTITY_CITY:
                entities = Object.values(getCities());
                break;
            case ENTITY_COUNTRY:
            default:
                entities = Object.values(getCountries());
                break;
        }
        // console.log(entities);
        return entities;
    }

    filterEntities() {
        // console.log(this.entities);
        if (this.independantOnly) {
            this.entities.filteredEntities = this.entities.filteredEntities.filter(
                (entity) => entity.independent === true
            );
        }
        if (this.region !== 'all') {
            this.entities.filteredEntities = this.entities.filteredEntities.filter((entity) =>
                entity.isInRegion(this.region)
            );
        }
        if (this.mode.questionType.key === 'city_map' && this.isNormal) {
            this.entities.filteredEntities = this.entities.filteredEntities.filter((city) => city.isCapital);
        }
        // console.log(this.entities);
    }

    generateQuizEntities(_gameLength) {
        if (!this.entities['quizEntities']) {
            const nbQuestions = this.getNbQuestions(_gameLength);
            if (_gameLength === SHORT) {
                this.entities['quizEntities'] = this.shuffle(this.entities.filteredEntities).slice(0, nbQuestions);
            } else {
                if (this.mode.nbQuestionLong) {
                    this.entities['quizEntities'] = this.shuffle(this.entities.filteredEntities).slice(0, nbQuestions);
                } else {
                    this.entities['quizEntities'] = this.shuffle(this.entities.filteredEntities);
                }
            }
        }
        return this.entities['quizEntities'];
    }

    getNbQuestions(_gameLength) {
        return _gameLength === SHORT
            ? this.mode.nbQuestionShort ?? NB_QUESTIONS_SHORT
            : this.mode.nbQuestionLong || undefined;
    }

    getEntities(_entityKey = 'allEntities') {
        // console.log(this.entities);
        if (!this.entities[_entityKey]) {
            this.entities[_entityKey] = this.generateEntitiesForType(_entityKey);
            // console.log(this.entities[_entityKey]);
        }
        return this.entities[_entityKey];
    }

    getQuestionTypeInstance(_entity, _forcedQuestionType) {
        // return the questionType instance, and if it does not exists yet, it creates it.
        let qType = _forcedQuestionType
            ? getQuestionType(this.gameConfig.questionTypes, _forcedQuestionType)
            : this.mode.questionType;
        if (Array.isArray(this.mode.questionType) && !_forcedQuestionType) {
            qType = this.getOneRandom(this.mode.questionType.filter((qt) => qt.entityType === _entity.entityType));
        }
        let qTypeKey = qType.key;
        if (!this.questionTypesInstances[qTypeKey]) {
            const questionTypesClass = this.questionTypesClasses[qTypeKey];
            this.questionTypesInstances[qTypeKey] = new questionTypesClass(this, qType);
        }
        return this.questionTypesInstances[qTypeKey];
    }

    generateQuestions(_chosenGameLength) {
        const reducedFilteredEntities = this.generateQuizEntities(_chosenGameLength);

        return this.shuffle(
            reducedFilteredEntities.map((entity) => {
                // console.log(entity);
                // const _forcedQuestionType = 'share_no_border';
                const _forcedQuestionType = undefined;
                return this.getQuestion(entity, _forcedQuestionType);
            })
        );
    }

    getQuestion(_entity, _forcedQuestionType) {
        let nbTry = 0;
        let question = undefined;
        let qTypeInstance;
        let questionType;

        while (!question) {
            nbTry++;
            if (nbTry > 5) {
                return undefined;
            }
            question = undefined;

            qTypeInstance = this.getQuestionTypeInstance(_entity, _forcedQuestionType);
            questionType = qTypeInstance.questionType;
            // console.log(questionType.key, _entity);
            question = qTypeInstance.getQuestion(_entity);
            // console.log(question);

            if (question) {
                if (questionType.questionProperty && (!question.questionText || question.questionText.length === 0)) {
                    question = undefined;
                } else if (!question?.choices && !this.isFlashcard) {
                    question = undefined;
                } else if (!question?.answer) {
                    question = undefined;
                }
            }
        }
        if (question?.choices && !this.isFlashcard) {
            // ajoute la bonne réponse parmis les choix
            question.choices.splice(((question.choices.length + 1) * Math.random()) | 0, 0, question.answer);
        }
        const q = {
            questionTypeKey: questionType.key,
            ...question,
            result: undefined,
            id: _entity.key,
            key: _entity.key,
        };
        // console.log(q);
        return q;
    }

    // todo: peakChoices filteredEntities vs allEntities selon si IS_NORMAL

    getChoicesList(_questionType, _choiceListKey, _isChoiceFlat) {
        // console.log(_choiceListKey);
        const choiceListKey =
            _questionType.choiceUseFilteredEntities && !this.mode.isTrivia ? 'filteredEntities' : _choiceListKey;
        if (!this.choicesLists[choiceListKey]) {
            this.generateChoicesList(choiceListKey, _questionType, _isChoiceFlat);
            // console.log(this.choicesLists);
        }
        return this.choicesLists[choiceListKey];
    }

    getSpecialChoicesList(_choiceListKey) {
        return this.choicesLists[_choiceListKey];
    }
    setSpecialChoicesList(_choiceListKey, _list) {
        this.choicesLists[_choiceListKey] = _list;
    }

    generateChoicesList(_key, _questionType, _isChoiceFlat) {
        // console.log(_questionType.choiceUseFilteredEntities + ' && ' + this.mode.isTrivia);
        if (_questionType.choiceEntityType) {
            let newList =
                this.entities[
                    _questionType.choiceUseFilteredEntities && !this.mode.isTrivia
                        ? 'filteredEntities'
                        : _questionType.choiceEntityType
                ];
            if (_questionType.choiceFilterProp && _questionType.choiceFilterVal !== undefined) {
                newList = this.getAllFilteredAnswers(newList, _questionType);
            }
            this.choicesLists[_key] = this.getAllAnswers(
                newList, // todo: utiliser getEntity avec la clé
                _questionType.choiceProperty,
                _isChoiceFlat,
                _questionType
            );
            // console.log(_key, _isChoiceFlat, newList);
        }
    }

    getAllFilteredAnswers(entities, _questionType) {
        const list = entities.filter(
            (e) => e.getValue(this.language, _questionType.choiceFilterProp) === _questionType.choiceFilterVal
        );
        // console.log(_questionType.choiceFilterProp, _questionType.choiceFilterVal, list);
        return list;
    }

    getAllAnswers(_entities, choiceProperty, _isChoiceFlat, _questionType) {
        // console.log(choiceProperty, _isChoiceFlat);
        if (!_isChoiceFlat) {
            const answers = {};
            let val;
            let reg;
            let subreg;
            _entities.forEach((entity) => {
                reg = entity.regionCode;
                subreg = entity.subregionCode;
                if (choiceProperty === 'languages') {
                    val = Object.keys(entity.languages);
                } else {
                    // val = entity.getValue(this.language, choiceProperty);
                    val = entity;
                }
                if (
                    !val ||
                    !entity.getValue(this.language, choiceProperty) ||
                    entity.getValue(this.language, choiceProperty) === ''
                ) {
                    return;
                }
                if (!answers[reg]) {
                    answers[reg] = {};
                }
                if (!answers[reg][subreg]) {
                    if (subreg === reg) {
                        answers[reg] = [];
                    } else {
                        answers[reg][subreg] = [];
                    }
                }
                answers[reg][subreg].push(...(Array.isArray(val) ? val : [val]));
            });

            if (choiceProperty === 'languages') {
                Object.keys(answers).forEach((reg) => {
                    Object.keys(answers[reg]).forEach((sub) => {
                        answers[reg][sub] = answers[reg][sub].filter(
                            (value, index, array) => array.indexOf(value) === index
                        );
                    });
                });
            }

            return answers;
        }
        if (choiceProperty === 'languages') {
            return _entities.reduceRight((cAnswers, entity) => {
                return cAnswers.concat(Object.keys(entity.languages));
                // } else {
                //     // return cAnswers.concat(entity.getValue(this.language, choiceProperty));
                //     return cAnswers.concat(entity);
            }, []);
        } else {
            return _entities;
        }
    }

    get isFlashcard() {
        return this.difficultyLevel === DIFFICULTY_FLASHCARD;
    }

    get isNormal() {
        return this.difficultyLevel === DIFFICULTY_NORMAL;
    }

    get isHard() {
        return this.difficultyLevel === DIFFICULTY_HARD;
    }

    get isExpert() {
        return this.difficultyLevel === DIFFICULTY_EXPERT;
    }

    shuffle(_list) {
        return _shuffle(_list);
    }

    getOneRandom(_list) {
        return _list && _list.length ? _list[Math.floor(Math.random() * _list.length)] : undefined;
    }
}
