import { ENTITY_COUNTRY } from 'config/baseConfig';

import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeLanguage extends QuestionType {
    getQuestion(_entity) {
        const countriesPerLanguages = this.getSpecialChoicesList(
            'countriesPerLanguages',
            this.generateCountriesPerLanguages.bind(this)
        );
        const countryLang = this.shuffle(Object.keys(_entity.languages)).find(
            (l) => countriesPerLanguages[l] && countriesPerLanguages[l].length >= 3
        );
        if (!countryLang) {
            return;
        }
        const questionText = _entity.languages[countryLang];
        const answer = _entity.getValue(this.language, 'name');
        const choices = this.getCountriesNotLanguage(_entity, countryLang, this.getChoicesList());
        return { questionText, answer, choices };
    }

    // getCountriesNotLanguage
    getCountriesNotLanguage(_entity, _countryLang, _possibleChoicesPerRegion) {
        if (this.isFlashcard) {
            return undefined;
        }
        const choices = [];
        const indexUsed = [];
        const max = _possibleChoicesPerRegion.length;
        let countryName;
        // console.log(_possibleChoicesPerRegion);
        let nbTry = 0;
        while (choices.length < this.nbChoices - 1) {
            nbTry++;
            if (nbTry > 10) {
                // console.log('while broke', _entity, choices);
                return undefined;
            }

            const rnd = Math.floor(Math.random() * max);
            countryName = _possibleChoicesPerRegion[rnd].getName(this.language);
            if (
                _entity.cca3 === _possibleChoicesPerRegion[rnd].cca3 || // same country than answer
                _possibleChoicesPerRegion[rnd].languages[_countryLang] !== undefined || // country has the language of the question
                indexUsed.includes(rnd) || // already used
                choices.includes(countryName) // already in choices
            ) {
                continue;
            }
            choices.push(countryName);
            indexUsed.push(rnd);
        }

        return choices;
    }

    generateCountriesPerLanguages() {
        let countriesPerLanguages = [];
        this.gameMode.getEntities(ENTITY_COUNTRY).forEach((entity) => {
            if (entity.languages) {
                Object.keys(entity.languages).forEach((l) => {
                    if (!countriesPerLanguages[l]) {
                        countriesPerLanguages[l] = [];
                    }
                    countriesPerLanguages[l].push(entity);
                });
            }
        });
        return countriesPerLanguages;
    }
}
