export class Region {
    constructor(obj) {
        // obj is the parsed json
        // Object.assign(this, obj);

        this.key = obj.code;
        this.code = obj.code;
        this.eng = obj.eng;
        this.fra = obj.fra;
        this.subregions = obj.subregions;
    }

    getName(lang) {
        return this[lang];
    }
}
