import { ENTITY_CITY } from 'config/baseConfig';

import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeIsOrNotCapital extends QuestionType {
    constructor(_gameMode, _questionType) {
        super(_gameMode, _questionType);
        this.otherType = { ..._questionType, choiceProperty: _questionType.answerProperty };
        this.otherTypeChoiceListKey = this.getOtherTypeKey(this.questionType);
    }

    getOtherTypeKey(_questionType) {
        let key = '';
        key += _questionType.choiceEntityType ? (key ? '_' : '') + _questionType.choiceEntityType : '';
        key += _questionType.answerProperty ? (key ? '_' : '') + _questionType.answerProperty : '';
        key += '_flat';
        return key;
    }

    generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty = false) {
        const choices = [];
        const expertChoices = this.isExpert && !_ignoreDifficulty ? [this.getHardAnswer(_rightAnswer)] : [];
        const indexUsed = [];
        let answersFrom = _possibleChoicesPerRegion;
        const otherTypeEntities = this.gameMode
            .getEntities(ENTITY_CITY)
            .filter((c) => c.isCapital === (this.questionType.answerProperty === 'capital'))
            .map((c) => c.getValue(this.language, 'name'));
        const max = answersFrom.length;

        let nbTry = 0;
        let tmpChoice;
        let tmpChoiceValue;
        while (choices.length < this.nbChoices - 1) {
            nbTry++;
            if (nbTry > 20) {
                // console.log('while broke', _entity, choices);
                return undefined;
            }
            const rnd = Math.floor(Math.random() * max);
            tmpChoice = answersFrom[rnd];
            tmpChoiceValue = tmpChoice.getValue(this.language, this.questionType.choiceProperty, 1);

            if (
                indexUsed.includes(rnd) ||
                tmpChoiceValue === '' ||
                tmpChoiceValue === undefined ||
                _rightAnswer.includes(tmpChoiceValue) ||
                otherTypeEntities.includes(tmpChoiceValue) ||
                choices.includes(tmpChoiceValue)
            ) {
                continue;
            }
            if (this.isExpert && !_ignoreDifficulty && expertChoices.includes(this.getHardAnswer(tmpChoiceValue))) {
                continue;
            }
            choices.push(tmpChoiceValue);
            indexUsed.push(rnd);
            if (this.isExpert && !_ignoreDifficulty) {
                expertChoices.push(this.getHardAnswer(tmpChoiceValue));
            }
        }

        return choices;
    }
}
