// import countriesData from 'data/countries.json';
// import lakesData from 'data/lakes.json';
// import landmarksData from 'data/landmarks.json';
// import peaksData from 'data/peaks.json';
import regionsData from 'data/regions.json';
// import seasData from 'data/seas.json';
import { City } from 'model/City';
import { Country } from 'model/Country';
import { Landmark } from 'model/Landmark';
import { Peak } from 'model/Peak';
import { Region } from 'model/Region';
import { SeaLake } from 'model/SeaLake';

let regions;
let countries = {};
let cities = [];
let peaks = [];
let landmarks = [];
let seas = {};
let lakes = {};
let seasAndLakes = {};

import('data/countries.json').then((data) => {
    initCountries(data.default);

    import('data/cities.json').then((data) => {
        initCities(data.default);
    });
});

import('data/peaks.json').then((data) => {
    initPeaks(data.default);
});

import('data/landmarks.json').then((data) => {
    initLandmarks(data.default);
});

import('data/seas.json').then((data) => {
    initSeas(data);

    import('data/lakes.json').then((data) => {
        initLakes(data.default);
        initSeasAndLakes(seas, lakes);
    });
});

// REGIONS

const initRegions = () => {
    const dict = {};
    [...Object.values(regionsData)].forEach((r) => {
        dict[r.code] = new Region(r);
        r.subregions?.forEach((s) => {
            dict[s.code] = new Region(s);
        });
    });
    regions = dict;
};
export const getRegions = () => {
    if (!regions) {
        initRegions();
    }
    return regions;
};

// COUNTRIES

const initCountries = (countriesData) => {
    const dict = {};
    [...Object.values(countriesData)].forEach((c) => {
        dict[c.cca3] = new Country(c);
    });
    countries = dict;
};
export const getCountries = () => {
    return countries;
};

// CITIES

const initCities = (citiesData) => {
    cities = [...Object.values(citiesData)].map((c) => new City(c));
};
export const getCities = () => {
    return cities;
};

// PEAKS

const initPeaks = (peaksData) => {
    let peaksHighestAndProminence = [
        ...Object.values(peaksData.byElevation).map((p) => new Peak(p)),
        ...Object.values(peaksData.byProminence).map((p) => new Peak(p)),
        ...Object.values(peaksData.otherFamous).map((p) => new Peak(p)),
    ];
    peaks = peaksHighestAndProminence.sort((p1, p2) => (p1.elevation > p2.elevation ? -1 : 1));
};
export const getPeaks = () => {
    return peaks;
};

// LANDMARKS

const initLandmarks = (landmarksData) => {
    landmarks = [...Object.values(landmarksData.natural), ...Object.values(landmarksData.constructed)].map(
        (l) => new Landmark(l)
    );
};
export const getLandmarks = () => {
    return landmarks;
};

// SEAS AND LAKES

const initSeas = (seasData) => {
    const dict = {};
    [...Object.values(seasData).sort((p1, p2) => (p1.area > p2.area ? -1 : 1))]
        .filter((s) => s.area && s.area > 1)
        .forEach((s) => {
            if (!s.disable) {
                dict[s.key] = new SeaLake(s);
            }
        });
    seas = dict;
};
const initLakes = (lakesData) => {
    const dict = {};
    [...Object.values(lakesData).sort((p1, p2) => (p1.area > p2.area ? -1 : 1))]
        .filter((s) => (s.area && s.area > 1) || s.min_label < 6)
        .forEach((s) => {
            if (!s.disable) {
                dict[s.key] = new SeaLake(s, false);
            }
        });
    lakes = dict;
};
const initSeasAndLakes = (seas, lakes) => {
    seasAndLakes = { ...seas, ...lakes };
};
export const getSeasAndLakes = () => {
    return seasAndLakes;
};
