import { GameMode } from 'store/quizBuilder/gameModes/GameMode';

export class GameModeCountry extends GameMode {
    filterEntities() {
        if (this.mode.questionType.questionProperty === 'capital') {
            this.entities.filteredEntities = this.entities.filteredEntities.filter(
                (country) => country.capital && country.capital?.length > 0
            );
        }
        super.filterEntities();
    }
}
