import { getCountries } from 'data/data';
import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeShareBorder extends QuestionType {
    constructor(_gameMode, _questionType) {
        super(_gameMode, _questionType);
        this.shareBorder = _questionType.key === 'share_border'; // otherwise share_no_border
    }

    getQuestion(_entity) {
        if (!_entity?.borders || _entity.borders.length < (this.shareBorder ? 2 : 3)) {
            // console.log(!_entity?.borders ? 'no borders' : null);
            return;
        }
        const questionText = this.getQuestionValue(_entity);
        let answer;
        let choices;
        if (this.shareBorder) {
            const oneBorder = this.getOneRandom(_entity.borders);
            const countryBorder = getCountries()[oneBorder];
            if (!countryBorder) {
                return;
            }
            answer = countryBorder.getName(this.language);
            choices = this.getChoices(_entity, countryBorder, this.getChoicesList());
        } else {
            answer = this.getChoices(_entity, _entity, this.getChoicesList());
            choices = this.shuffle(_entity.borders)
                .slice(0, 3)
                .map((b) => getCountries()[b].getName(this.language));
        }
        return { questionText, answer, choices };
    }

    // getCountriesNotBorder
    generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty) {
        const choices = [];
        const choices_cca3 = [];
        const onlyOne = _entity === _rightAnswer;
        let nbChoices = 0;
        let countryBorder;
        let countryWithoutBorder;
        let countryWithoutBorderCommonName;
        if (!_entity || !_rightAnswer) {
            console.trace();
        }

        let allBorders;
        if (this.isNormal) {
            // choix parmis tous les pays
            allBorders = _possibleChoicesPerRegion.filter((c) => !_entity.borders.includes(c.cca3)).map((c) => c.cca3);
        }
        if (this.isHard) {
            // choix parmis les pays de même région
            allBorders = _possibleChoicesPerRegion
                .filter((c) => !_entity.borders.includes(c.cca3) && _entity.regionCode === c.regionCode)
                .map((c) => c.cca3);
        } else {
            // choix parmis les pays adjacents voisins
            allBorders = this.shuffle([...new Set([..._entity.borders, ..._rightAnswer.borders])]);
        }
        // console.log(_entity.getName(this.language), allBorders);

        let i = 0;
        let nbTry = 0;
        while (nbChoices < this.nbChoices - 1 && i < allBorders.length) {
            nbTry++;
            if (nbTry > 20) {
                // console.log('while broke', _entity, choices);
                return undefined;
            }
            countryBorder = getCountries()[allBorders[i]];
            if (!countryBorder) {
                // console.log(_entity);
                console.trace();
                continue;
            }
            countryWithoutBorder = this.shuffle(countryBorder.borders).find(
                (b) => !_entity.borders.includes(b) && !choices_cca3.includes(b) && b !== _entity.cca3
            );
            if (countryWithoutBorder) {
                countryWithoutBorder = getCountries()[countryWithoutBorder];
                countryWithoutBorderCommonName = countryWithoutBorder.getName(this.language);

                if (onlyOne) {
                    return countryWithoutBorderCommonName;
                }
                if (countryWithoutBorderCommonName === '') {
                    continue;
                }
                choices.push(countryWithoutBorderCommonName);
                choices_cca3.push(countryWithoutBorder.cca3);
                nbChoices = choices.length;
            }
            i++;
        }

        if (nbChoices < this.nbChoices - 1) {
            return undefined;
        }

        return choices;
    }
}
