import { ENTITY_LANDMARK } from 'config/baseConfig';

import { getCountries } from 'data/data';

export class Landmark {
    constructor(obj) {
        this.isLandmark = true;
        this.entityType = ENTITY_LANDMARK;

        this.key = obj.name;
        this.name = obj.name;
        this.name_complete = obj.name_complete;
        this.type = obj.type.split(',');
        this.country = obj.country.map((c) => getCountries()[c]);

        this.images = obj.images;
        this.coord = obj.coord;
    }

    getName(lang) {
        return this.name_complete ?? this.name;
    }
    getNameForQuiz(lang) {
        return this.name;
    }
    getRegion(lang) {
        return this.country
            .map((c) => (c.subregion ? c.subregion : c.region)[lang])
            .filter((value, index, array) => array.indexOf(value) === index)
            .join(', ');
    }
    get regionCode() {
        return this.country?.[0].regionCode;
    }
    get subregionCode() {
        return this.country?.[0].subregionCode;
    }

    getValue(lang, valueName, nbFromList) {
        let value;
        switch (valueName) {
            case 'name':
                value = this.getName(lang);
                break;
            case 'name-incomplete':
                value = this.getNameForQuiz(lang);
                break;
            case 'country':
                value = this.country.map((c) => c.getName(lang));
                // value = nbFromList === 1 ? this.getOneRandom(country) : country;
                break;
            default:
                value = this[valueName];
                break;
        }
        // console.log(value);
        return value;
    }

    isInRegion(region) {
        return !!this.country.find((c) => c.regionCode === region);
    }
}
