import { ROUTE_TEST } from 'config/routes';
import { lazy, useEffect, useState } from 'react';
import ReactGA from 'react-ga4';
import { useDispatch } from 'react-redux';
import { useLocation } from 'react-router';

import { CssBaseline, ThemeProvider } from '@mui/material';

import Layout from 'components/layout/Layout';
import Menu from 'components/menu/Menu';
import { setConfig } from 'store/actions';
// import { QuizBuilder } from 'store/quizBuilder/QuizBuilder';
import { useGameConfig, useIsDarkMode, usePage } from 'store/selector';
import { createTheme as createMyTheme } from 'theme';
import { createTheme as createMyThemeDark } from 'theme/dark';

// import Wiki from 'components/wiki/Wiki';
const Wiki = lazy(() => import('components/wiki/Wiki'));

// import Game from 'components/game/Game';
const Game = lazy(() => import('components/game/Game'));

// const Menu = lazy(() => import('components/menu/Menu'));

const theme = createMyTheme();
const darkTheme = createMyThemeDark();

const GeoGoApp = () => {
    const page = usePage();
    const isDarkMode = useIsDarkMode();
    const location = useLocation();
    const gameConfig = useGameConfig();
    const dispatch = useDispatch();

    const [deferredPrompt, setDeferredPrompt] = useState(undefined);
    const TRACKING_ID = 'G-ZKS24WBKJK'; // YOUR_OWN_TRACKING_ID

    // const { i18n } = useTranslation();
    // console.log(i18n);
    const isDev = !process.env.NODE_ENV || process.env.NODE_ENV === 'development';
    if (!isDev) {
        ReactGA.initialize(TRACKING_ID);
    }

    const selectedTheme =
        isDarkMode ||
        (isDarkMode === undefined && window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches)
            ? darkTheme
            : theme;

    useEffect(() => {
        dispatch(setConfig(location));
    }, [location]);

    useEffect(() => {
        if (location.pathname === ROUTE_TEST) {
            // console.log('try');
            // try {
            //     const quizBuilder = new QuizBuilder(gameConfig);
            //     quizBuilder.testAll(language);
            //     console.log('fin test');
            // } catch (e) {
            //     console.log(e);
            // }
        }
    }, [gameConfig]);

    useEffect(() => {
        window.addEventListener('beforeinstallprompt', function (event) {
            // Prevent Chrome 67 and earlier from automatically showing the prompt
            event.preventDefault();
            // Stash the event so it can be triggered later.
            setDeferredPrompt(event);
        });
    }, []);

    const inGame = page === 'game';
    const inWiki = page === 'wiki';

    return (
        <ThemeProvider theme={selectedTheme}>
            <CssBaseline />
            {/*isDev && <>dev</>*/}
            <Layout deferredPrompt={deferredPrompt}>{inGame ? <Game /> : inWiki ? <Wiki /> : <Menu />}</Layout>
            {/*<Box position="absolute" left={0} right={0} top={0} botom={0}>
                {Object.values(countries)
                    .filter((c) => c?.flagSameAs?.length)
                    .map((country) => (
                        <GameFlag
                            country={country.cca3}
                            svgOverrides={{
                                maxHeight: '5vh',
                            }}
                        />
                    ))}
            </Box>*/}
        </ThemeProvider>
    );
};

export default GeoGoApp;
