import ReactGA from 'react-ga4';

import { GameUtil } from 'model/game/GameUtil';
import * as constants from 'store/constants';
import { QuizBuilder } from 'store/quizBuilder/QuizBuilder';

export const defaultSettings = {
    showAdvancedOptions: false,
    isDarkMode: false,
    language: 'eng',
};

const defaultState = {
    settings: defaultSettings,
    isInit: false,
    inGame: false,
    inWiki: false,
    currentGame: undefined,
    lastGameModes: [],
};
// console.log(defaultState);

// const gameDefaultState = {
//     version: GAME_VERSION,
//     currentScore: 0,
//     currentTurn: 0,
//     currentPhase: 0,
//     // Used
//     gameMode: CAPITAL,
//     region: 'all',
//     independantOnly: false,
//     difficultyLevel: DIFFICULTY_NORMAL,
//     gameLength: SHORT,
//     questions: {},
// };

const reducer = (state = defaultState, { type, ...payload }) => {
    // console.log(state, type, payload);
    const game = state.currentGame;
    const settings = state.settings;
    let lastGameModes;

    switch (type) {
        case constants.INIT_CONFIG:
            // gameConfig: { ...gameConfig, nbChoices: NB_CHOICES, difficultyLevels: difficultyLevels },
            return {
                ...state,
                gameConfig: { ...payload.gameConfig },
            };

        case constants.EDIT_SETTINGS:
            return {
                ...state,
                settings: { ...state.settings, ...payload.settings },
            };

        case constants.IS_INIT:
            return {
                ...state,
                isInit: payload.isInit,
            };
        case constants.SET_PAGE:
            return {
                ...state,
                page: payload.page,
            };

        case constants.NEW_GAME:
            return {
                ...state,
                currentGame: undefined,
                page: 'menu',
                gameOptions: game
                    ? {
                          gameMode: game.gameMode,
                          region: game.region,
                          independantOnly: game.independantOnly,
                          difficultyLevel: game.difficultyLevel,
                          gameLength: game.gameLength,
                          hideBackgroundMap: game.hideBackgroundMap,
                      }
                    : undefined,
            };

        case constants.CHOOSE_GAMECATEGORY:
            lastGameModes = { ...state.lastGameModes } ?? {};
            lastGameModes['main'] = payload.chosenGameCategory;
            return {
                ...state,
                lastGameModes,
            };
        case constants.CHOOSE_GAMEMODE:
            lastGameModes = { ...state.lastGameModes } ?? {};
            lastGameModes[lastGameModes['main'] ?? state.gameOptions.gameMode] = payload.chosenGameMode;
            return {
                ...state,
                lastGameModes,
            };

        case constants.START_GAME:
            const chosenGameMode = payload.chosenGameMode ?? state.currentGame.gameMode;
            const chosenRegion = payload.chosenRegion ?? state.currentGame.region;
            const chosenDifficultyLevel = payload.chosenDifficultyLevel ?? state.currentGame.difficultyLevel;
            const chosenIndependantOnly = state.settings.showAdvancedOptions
                ? payload.chosenIndependantOnly ?? state.currentGame.independantOnly ?? true
                : true;
            const chosenGameLength = payload.chosenGameLength ?? state.currentGame.gameLength;
            const chosenHideBackgroundMap = payload.chosenHideBackgroundMap ?? state.currentGame.hideBackgroundMap;

            const mode = state.gameConfig.gameModes[chosenGameMode];

            const quizBuilder = new QuizBuilder(state.gameConfig);

            const questions = quizBuilder.generateQuestions(
                settings.language,
                mode,
                chosenRegion,
                chosenDifficultyLevel,
                chosenIndependantOnly,
                chosenGameLength
            );

            return {
                ...state,
                page: 'game',
                currentGame: {
                    ...GameUtil.getGameDefaultState(),
                    gameMode: mode.key,
                    region: chosenRegion,
                    independantOnly: chosenIndependantOnly,
                    difficultyLevel: chosenDifficultyLevel,
                    gameLength: chosenGameLength,
                    hideBackgroundMap: chosenHideBackgroundMap,
                    questions: questions,
                },
            };

        case constants.GAME_NEW_QUESTION:
            return {
                ...state,
                currentGame: {
                    ...game,
                    currentPhase: 1,
                },
            };

        case constants.GAME_ANSWER:
            const chosenAnswer = payload.chosenAnswer;
            const allQuestions = [...game.questions];
            const question = { ...allQuestions[game.currentTurn] };
            const result = (chosenAnswer?.value ?? chosenAnswer) === (question.answer?.value ?? question.answer);

            const currentScore = result ? game.currentScore + 1 : game.currentScore;

            return {
                ...state,
                currentGame: {
                    ...game,
                    questions: allQuestions.map((q, i) => {
                        return game.currentTurn === i ? { ...q, result } : q;
                    }),
                    currentPhase: 2,
                    currentScore,
                },
            };

        case constants.GAME_NEXT:
            const newTurn = game.currentTurn + 1;
            const isEnd = newTurn >= game.questions.length;
            if (isEnd) {
                ReactGA.event({
                    category: 'game',
                    action: 'end',
                    label: `${game.gameMode}-${game.region}-${game.difficultyLevel}`, // optional
                    value: game.currentScore / game.questions.length, // optional
                });
            }

            return {
                ...state,
                currentGame: {
                    ...game,
                    currentTurn: newTurn,
                    currentPhase: 0,
                },
            };

        default:
            return state;
    }
};

export default reducer;
