import { useTheme } from '@emotion/react';
import * as React from 'react';
import { useState } from 'react';
import { lazy } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';

import { Box, Grow, Stack, Typography } from '@mui/material';

import { TOP_NAV_HEIGHT_REM } from 'components/layout/Header';
// import NewGame from 'components/menu/NewGame';
import FunTypo from 'components/ui/FunTypo';
import MainButton from 'components/ui/MainButton';
import useWindowDimensions from 'hooks/useWindowDimensions';
import { setIsInit, setPage } from 'store/actions';
import { useGameConfig, useIsInit } from 'store/selector';
import { pxToRem } from 'util/util';

// import Menu from 'components/menu/Menu';
const NewGame = lazy(() => import('components/menu/NewGame'));

const SCREEN_HOME = 0;
const SCREEN_GAME_OPTIONS = 1;

const Menu = () => {
    const windowDimensions = useWindowDimensions();
    const theme = useTheme();
    const isInit = useIsInit();
    const gameConfig = useGameConfig();
    const [screen, setScreen] = useState(isInit ? 1 : 0);
    const [isInitStarted, setIsInitStarted] = useState(isInit);

    const dispatch = useDispatch();
    const { t } = useTranslation();

    const handleClickWiki = () => {
        dispatch(setPage('wiki'));
    };

    const doSetIsinit = () => {
        dispatch(setIsInit(true));
        setScreen(SCREEN_GAME_OPTIONS);
    };
    const doSetIsInitStarted = () => {
        setIsInitStarted(true);
    };

    if (!isInit) {
        setTimeout(doSetIsinit, 500);
        setTimeout(doSetIsInitStarted, 100);
    }

    const isLandscape = windowDimensions.width > windowDimensions.height && windowDimensions.height < 750;

    const showHome = screen === SCREEN_HOME;
    const showBigHomeLogo = showHome && !isLandscape;

    return (
        <>
            <Stack height="100%" flexGrow={1} position="relative" sx={{}}>
                <Box
                    position="absolute"
                    margin="0 auto"
                    width="100%"
                    top={showBigHomeLogo ? 50 : `calc(-${TOP_NAV_HEIGHT_REM}/2)`}
                    overflow="hidden"
                    sx={{
                        height: `calc(${showBigHomeLogo ? 30 : 20}vh - ${TOP_NAV_HEIGHT_REM})`,
                        minHeight: pxToRem(showBigHomeLogo ? 205 : 150),
                        transition: 'all 0.25s ease',
                        pointerEvents: 'none',
                    }}
                >
                    <Typography variant="h1" display="block">
                        <FunTypo
                            text={t(gameConfig?.name)}
                            color={theme.palette.text.title.replace('#', '')}
                            stroke={true}
                            strokeWidth={pxToRem(2)}
                            distance={pxToRem(6 * (showBigHomeLogo ? 1 : 0.7))}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(100 * (showBigHomeLogo ? 1 : 0.7)),
                                lineHeight: pxToRem(92 * (showBigHomeLogo ? 1 : 0.7)),
                                fontWeight: 800,
                                textAlign: 'center',
                                mb: pxToRem(-36 * (showBigHomeLogo ? 1 : 0.7)),
                                transition: !isInit ? '0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275)' : '0.25s ease',
                                transform: !isInit && !isInitStarted ? `translate(${pxToRem(-100)}, 0px)` : 'none',
                                opacity: !isInit && !isInitStarted ? 0 : 1,
                            }}
                        />
                        <FunTypo
                            text={t('Quiz')}
                            color={theme.palette.text.title.replace('#', '')}
                            stroke={false}
                            strokeWidth={pxToRem(2)}
                            distance={pxToRem(7 * (showBigHomeLogo ? 1 : 0.7))}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(110 * (showBigHomeLogo ? 1 : 0.7)),
                                lineHeight: pxToRem(108 * (showBigHomeLogo ? 1 : 0.7)),
                                fontWeight: 800,
                                textAlign: 'center',
                                transition: !isInit ? '0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275)' : '0.25s ease',
                                marginTop: pxToRem(10 * (showBigHomeLogo ? 1 : 0.7)),
                                transform: !isInit && !isInitStarted ? `translate(${pxToRem(100)}, 0px)` : 'none',
                                opacity: !isInit && !isInitStarted ? 0 : 1,
                            }}
                        />
                    </Typography>
                </Box>
                {!isLandscape ? (
                    <>
                        {/*showHome && (
                            <Box width="100%" height="40vh" position="absolute" top="calc(50% - 120px)">
                                <SvgIcon
                                    sx={{
                                        height: pxToRem(240),
                                        width: pxToRem(240),
                                        display: 'block',
                                        margin: '0 auto',
                                        color:
                                            theme.palette.mode === 'dark'
                                                ? theme.palette.primary.light
                                                : theme.palette.secondary.main,
                                        opacity: theme.palette.mode === 'dark' ? 0.5 : 1,
                                    }}
                                >
                                    <GlobeIcon />
                                </SvgIcon>
                            </Box>
								)*/}
                        {!showHome && (
                            <Grow in={true} key="home">
                                <Box
                                    display="flex"
                                    alignItems="center"
                                    flexDirection="column"
                                    justifyContent="flex-start"
                                    width="100%"
                                    position="absolute"
                                    sx={{
                                        height: '69vh',
                                        top: showHome
                                            ? 'calc(40vh - ' + TOP_NAV_HEIGHT_REM + ')'
                                            : 'calc(20vh - ' + TOP_NAV_HEIGHT_REM + ')',
                                    }}
                                >
                                    <NewGame />
                                </Box>
                            </Grow>
                        )}
                        <Box
                            display="flex"
                            alignItems="center"
                            flexDirection="row"
                            justifyContent="center" // {showHome ? 'center' : 'flex-start'}
                            width="100%"
                            position="absolute"
                            bottom="0"
                            sx={{
                                height: showHome ? '30vh' : '10vh',
                            }}
                        >
                            {isInit && (
                                <>
                                    <MainButton
                                        buttonP={{
                                            color: 'success',
                                            onClick: handleClickWiki,
                                        }}
                                    >
                                        {t('Wiki')}
                                    </MainButton>
                                </>
                            )}
                        </Box>
                    </>
                ) : (
                    <Box
                        display="flex"
                        alignItems="center"
                        flexDirection="column"
                        justifyContent="center"
                        width="100%"
                        height="65vh"
                        position="absolute"
                        bottom="0"
                    >
                        <Typography
                            variant="p"
                            display="block"
                            textAlign="center"
                            margin="0 auto"
                            maxWidth="400px"
                            sx={{
                                fontSize: pxToRem(32),
                                lineHeight: pxToRem(32),
                                fontWeight: 800,
                            }}
                        >
                            {t('Please use your device in portrait mode for an optimal experience')}
                        </Typography>
                    </Box>
                )}
            </Stack>
        </>
    );
};

export default Menu;
