import { ENTITY_COUNTRY } from 'config/baseConfig';

import { getRegions } from 'data/data';

export class Country {
    constructor(obj) {
        // obj is the parsed json
        // Object.assign(this, obj);
        this.isCountry = true;
        this.entityType = ENTITY_COUNTRY;
        const regions = getRegions();

        this.key = obj.cca3;
        this.cca3 = obj.cca3;
        this.flagSameAs = obj.flagSameAs;
        this.cca2 = obj.cca2;
        this.landlocked = obj.landlocked;
        this.independent = obj.independent;
        this.sovereignState = obj.sovereignState;

        this.borders = obj.borders;
        this.area = obj.area;
        this.languages = obj.languages;
        this.currencies = obj.currencies;
        this.demonyms = obj.demonyms;

        this.regionCode = obj.region;
        this.subregionCode = obj.subregion;
        this.region = regions[obj.region];
        this.subregion = regions[obj.subregion];

        this.translations = obj.translations;

        this.capital = [];
        this.cities = [];
    }

    addCapital(capital) {
        this.capital?.push(capital);
    }
    addCity(city) {
        this.cities?.push(city);
    }

    getCapital(lang) {
        return this.capital.map((c) => c.name[lang]);
    }
    getCities(lang) {
        return this.cities?.map((c) => c.name[lang]);
    }
    getName(lang) {
        return this.translations[lang].common;
    }
    getOfficialName(lang) {
        return this.translations[lang].official;
    }

    getValue(lang, valueName, nbFromList) {
        let value;
        switch (valueName) {
            case 'translations':
                value = this.getCountryTranslations(lang);
                break;
            case 'name-official':
                value = this.getOfficialName(lang);
                break;
            case 'name':
                value = this.getName(lang);
                break;
            case 'capital':
                let capitals = this.getCapital(lang);
                value = nbFromList === 1 ? this.getOneRandom(capitals) : capitals;
                break;
            case 'cities':
                let cities = this.getCities(lang);
                value = nbFromList === 1 ? this.getOneRandom(cities) : cities;
                break;
            case 'borders':
                value = nbFromList === 1 ? this.getOneRandom(this.borders) : this.borders;
                break;
            case 'peak':
                value = this.getCountryPeak();
                break;
            default:
                value = this[valueName];
                break;
        }
        return value;
    }
    getOneRandom(list) {
        return list && list.length ? list[Math.floor(Math.random() * list.length)] : undefined;
    }
    getCountryPeak() {
        return this.peaks !== undefined && this.peaks.length > 0 ? this.peaks[0].name : undefined;
    }
    getCountryTranslations(lang) {
        return this.translations[lang];
    }

    isInRegion(region) {
        return this.regionCode === region;
    }
}
