import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeFlag extends QuestionType {
    canBeInChoices(
        _ignoreDifficulty,
        _rightAnswer,
        choices,
        indexUsed,
        rnd,
        _entity,
        tmpChoice,
        tmpChoiceValue,
        expertChoices
    ) {
        return (
            super.canBeInChoices(
                _ignoreDifficulty,
                _rightAnswer,
                choices,
                indexUsed,
                rnd,
                _entity,
                tmpChoice,
                tmpChoiceValue,
                expertChoices
            ) && !_entity.flagSameAs?.includes(tmpChoice.cca3)
        );
    }
}
