import {
    DIFFICULTY_EXPERT,
    DIFFICULTY_FLASHCARD,
    DIFFICULTY_HARD,
    DIFFICULTY_NORMAL,
    GAME_VERSION,
    SHORT,
} from 'config/baseConfig';

export class GameUtil {
    // constructor(obj) {
    //     // obj is the parsed json
    //     Object.assign(this, obj);
    //     // console.log(obj);
    // }

    static getGameDefaultState() {
        return {
            version: GAME_VERSION,
            currentScore: 0,
            currentTurn: 0,
            currentPhase: 0,
            // Used
            // gameMode: CAT_CAPITAL,
            region: 'all',
            independantOnly: false,
            difficultyLevel: DIFFICULTY_NORMAL,
            gameLength: SHORT,
            questions: [],
        };
    }

    static getIsEnd(_game) {
        return _game.currentTurn >= _game.questions.length;
    }
    static getNextKeyLabel(_game) {
        return _game.currentTurn + 1 >= _game.questions.length ? 'End' : 'Next';
    }
    static getProgress(_game) {
        return `${_game.currentTurn + 1} / ${_game.questions.length}`;
    }
    static getProgressValue(_game) {
        return ((_game.currentTurn + 1) / _game.questions.length) * 100;
    }
    static getProgressValueBuffer(_game) {
        return ((_game.currentTurn + (_game.currentPhase === 2 ? 1 : 0)) / _game.questions.length) * 100;
    }
    static getQuestion(_game) {
        return _game.questions && _game.questions[_game.currentTurn];
    }
    static getNextQuestion(_game) {
        return _game.questions && _game.questions.length >= _game.currentTurn && _game.questions[_game.currentTurn + 1];
    }
    static isFlashcard(_game) {
        return _game.difficultyLevel === DIFFICULTY_FLASHCARD;
    }
    static isNormal(_game) {
        return _game.difficultyLevel === DIFFICULTY_NORMAL;
    }
    static isHard(_game) {
        return _game.difficultyLevel === DIFFICULTY_HARD;
    }
    static isExpert(_game) {
        return _game.difficultyLevel === DIFFICULTY_EXPERT;
    }
}
