import { t } from 'i18next';

import { QuestionType } from 'store/quizBuilder/questionTypes/QuestionType';

export class QuestionTypeCapital extends QuestionType {
    getQuestion(_entity) {
        const questionText = this.getQuestionValue(_entity);
        const allCountryCap = this.shuffle(_entity.getValue(this.language, this.questionType.answerProperty));
        const choices = this.getChoices(_entity, allCountryCap, this.getChoicesList());
        const answer = allCountryCap?.pop();
        const answerAdditionnalText = this.getAdditionnalText(_entity);
        return { questionText, answer, choices, answerAdditionnalText };
    }

    getAdditionnalText(_entity, _allCountryCap) {
        return _allCountryCap && _allCountryCap.length > 0 ? `${t('and')} ` + _allCountryCap.join(', ') : undefined;
    }

    // getCapitalChoices
    generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion, _ignoreDifficulty) {
        const choices = super.generateChoices(_entity, _rightAnswer, _possibleChoicesPerRegion);

        if (this.isHard /* || difficultyLevel !== DIFFICULTY_EXPERT*/) {
            // on inclut aussi des villes non capitales de temps en temps
            if (_entity.cities && _entity.cities.length && Math.random() * 2 < 1) {
                const otherCity = _entity.getValue(this.language, 'cities', 1);
                if (otherCity && otherCity !== '') {
                    if (
                        !this.isExpert ||
                        !choices.some((c) => this.getHardAnswer(otherCity) === this.getHardAnswer(c))
                    ) {
                        choices[Math.floor(Math.random() * choices.length)] = otherCity;
                    }
                }
            }
        }

        return choices;
    }
}
